import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Button, ButtonColor, ButtonType, Badge, BadgeColor, ButtonGroup, HoverButton, ButtonSize } from '../../../button/model';
import { CollectionUtils } from '../../../utility';
import { CrudDescription, CrudHeaderIcon, CrudStyle } from '../../model';
import { FormDiaplyMode, FormTitleIconPosition } from '../../../form/model';
import { AbilityUtils } from '../../../utility/ability.utility';
import { Ability } from '@casl/ability';

@Component({
  selector: 'mx-crud-header',
  templateUrl: './crud-header.component.html',
  styleUrls: ['./crud-header.component.scss']
})
export class CrudHeaderComponent implements OnInit {
  @Input() identifier: string;
  @Input() title: string; 
  @Input() subtitle: string; 
  @Input() description: CrudDescription;
  @Input() badges: Array<Badge>;
  @Input() icon: CrudHeaderIcon;
  @Input() style: CrudStyle;

  @Input() _pageBackRoute: Array<string>;
  get pageBackRoute(): Array<string> {
    return this._pageBackRoute;
  }
  @Input()
  set pageBackRoute(_pageBackRoute: Array<string>) {
    this._pageBackRoute = _pageBackRoute;
    this.addBackButton();
  } 

  @Input() formDisplayMode: FormDiaplyMode;
  @Input() actions: Array<Button | HoverButton | ButtonGroup>;
  @Input() showHeader: boolean;
  @Input() originalData: any;
  @Input() context: any;
  @Output() onButtonClick = new EventEmitter();

  iconPosition: FormTitleIconPosition;
  badgeButtons: Array<ButtonGroup> = new Array<ButtonGroup>();

  constructor(private ability: Ability) { 
    AbilityUtils.setAbility(this.ability);
  }

  ngOnInit() {
    this.setIconPosition();
    this.addBackButton();
    this.transformBadgeButtons();
  }

  setIconPosition() {
    this.iconPosition = FormTitleIconPosition.BEFORE_TITLE;
    if (this.icon && this.icon.position)  {
      this.iconPosition = this.icon.position;
    }
  }

  transformBadgeButtons(): void {
    if (this.badges && this.badges.length > 0) {
      let index: number = 0;
      for (let badge of this.badges) {
        this.badgeButtons.push({
          identifier: 'crudHeaderBadge-' + index,
          type: ButtonType.CHIP,
          label: badge.content,
          color: this.badgeColorToButtonColor(badge.color),
          size: ButtonSize.MICRO,
          groupIdentifier: "crudHeaderGroup",
        });
      }
    }
  }

  badgeColorToButtonColor(color: BadgeColor): ButtonColor {
    let buttonColor: ButtonColor;
    if (color == BadgeColor.ASCENT) {
      buttonColor = ButtonColor.ASCENT;
    } else if (color == BadgeColor.PRIMARY) {
      buttonColor = ButtonColor.PRIMARY;
    } else if (color == BadgeColor.WARN) {
      buttonColor = ButtonColor.WARN;
    }

    return buttonColor;
  }

  addBackButton(): void {
    if (!this._pageBackRoute || CollectionUtils.isEmpty(this._pageBackRoute)) {
      return;
    }

    let backButton: Button;

    if (CollectionUtils.isEmpty(this.actions)) {
      this.actions = new Array<Button | HoverButton | ButtonGroup>();
    }

    backButton = {
      identifier: "crudBackButton",
      label: "Back",
      color: ButtonColor.PRIMARY,
      size: ButtonSize.SMALL,
      icon: "keyboard_return",
      type: ButtonType.GHOST,
      onlyIcon: false,
      routerLink: this._pageBackRoute,
      displayInFormModes: [
        FormDiaplyMode.CRUD_FORM
      ]
    };

    let backAlreadyAdded = false;
    for (let action of this.actions) {
      if (action.identifier == "crudBackButton") {
        backAlreadyAdded = true;
      }
    }

    if (!backAlreadyAdded) {
      this.actions.unshift(backButton);

      this.actions = JSON.parse(JSON.stringify(this.actions));
    }
  }

  buttonClick(event: any) {
    console.log(event);
    this.onButtonClick.emit(event);
  }
}
