import { Field, FieldType, TextField, AutocompleteField, CheckboxField, RadioField, DropdownField, DropdownOption, KeyMap, DisplayMode } from '../field/model';
import { FormDiaplyMode } from '../form/model';
import { MasterDataUtils } from './master-data.utility'; 
import { StringUtils } from './string.utility';
import { CollectionUtils } from './collection.utility';

export class FieldUtils {
  static isFormDisable(displayMode: FormDiaplyMode): boolean {
    if (displayMode == FormDiaplyMode.BLOCKED || displayMode == FormDiaplyMode.VIEW) {
      return true;
    } else { 
      return false;
    }
  }

  static isFieldDisabled(field: Field, currentDisplayMode: FormDiaplyMode, value: string | Array<string>): boolean {
    let isDisabled: boolean = false;
    if (FieldUtils.isFormDisable(currentDisplayMode) || field.displayMode == DisplayMode.DISABLED || field.type == FieldType.HIDDEN || field.isReadOnly || (field.isUnique && currentDisplayMode == FormDiaplyMode.EDIT && !StringUtils.isEmpty(value))) {
      isDisabled = true;
    }

    return isDisabled;
  }

  static displayEllipsis(charLimit: number, value: any): boolean {
    let showEllipsis: boolean = false;

    let strValue = JSON.stringify(value);
    if (charLimit && charLimit > 0 && !StringUtils.isEmpty(strValue) && strValue.length > charLimit) {
      showEllipsis = true;
    }
    return showEllipsis;
  }

  static readOnlyField(): Array<string> {
    return [FieldType.IMAGE, FieldType.IMAGE_AND_TEXT, FieldType.JSON, FieldType.LABEL, FieldType.BOOLEAN];
  }

  static isEllipsisField(field: Field) {
    let hasEllipsis: boolean;

    if ((field.type == FieldType.LABEL || field.type == FieldType.TEXT || field.type == FieldType.TEXTAREA || field.type == FieldType.EMAIL || field.type == FieldType.PASSWORD) && (<TextField>field).ellipsis > 0) {
      hasEllipsis = true;
    }

    return hasEllipsis;
  }

  static setOptionsUsingKey(field: AutocompleteField | CheckboxField | RadioField | DropdownField, masterDataKey: string) {
    if (!CollectionUtils.isEmpty(field)) {
      let options: Array<DropdownOption> = MasterDataUtils.getMasterDataAsOptions(masterDataKey);

      field.options = options;
    }
  }

  static setOptionsUsingValues(field: AutocompleteField | CheckboxField | RadioField | DropdownField, keyMap: KeyMap) {
    if (!CollectionUtils.isEmpty(field)) {
      field.options = keyMap.options; 
    }
  }
}