import { Component, OnInit, Inject, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { Form } from '../../../form/model';
import { List } from '../../../list/model';
import { KeyMap, FieldChange } from '../../../field/model';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AbilityUtils } from '../../../utility/ability.utility';
import { Ability } from '@casl/ability';

export interface DialogData {
  type: string,
  widgetConfig: Form | List,
  sourceIndex: number,
  context: any,
  originalData: any,
  keyMap: Array<KeyMap>,
  reset: boolean,
  currentInstance: any
}

@Component({
  selector: 'mx-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ModalComponent implements OnInit {
  @Output() onFormChange = new EventEmitter();
  @Output() onFieldChange = new EventEmitter();
  @Output() onButtonClick = new EventEmitter();

  constructor(public dialogRef: MatDialogRef<ModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private ability: Ability) { 
      AbilityUtils.setAbility(this.ability);
    }

  ngOnInit(): void {
  }

  click(event: any) {
    this.dialogRef.close(event);
  }

  fieldChange(fieldChange: FieldChange) {
    console.log(fieldChange);
    this.onFieldChange.emit(fieldChange);
  }

  formChange(form: any) {
    this.onFormChange.emit(form);
  }

  buttonClick(event: any) {
    console.log(event);
    this.onButtonClick.emit(event);
  }
}
