import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MultiImage, MultiImageField } from '../../model/multi-image-field.model';

@Component({
    selector: 'mx-multi-image',
    templateUrl: './multi-image.component.html',
    styleUrls: ['./multi-image.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class MultiImageComponent implements OnInit {
    @Input() field: MultiImageField = null;
    @Input() control: FormControl;
    @Input() disabled: boolean;

    radius: number;
    count: number;
    showCount: boolean;
    showAll: boolean;
    images: MultiImage[] | string[];
    lastItemLabel: string;
    showLastItem: boolean;
    hideTooltip: boolean;

    constructor() { }

    ngOnInit() {
        this.radius = (this.field.radius || 5) * 10;

        this.showCount = this.field.showCount || false;
        this.showAll = this.field.showAll || false;
        this.images = this.field.images;
        this.images = this.images ? this.images : <Array<string> | Array<MultiImage>>this.field.value;
        this.count = this.field.count || (this.images ? this.images.length : 0);
        
        if (this.showCount && (this.showAll || !this.showAll)) {
            this.lastItemLabel = '+' + (this.images.length - this.count);
        } else if (this.showAll && !this.showCount) {
            this.lastItemLabel = '>';
        }

        this.hideTooltip = this.images && this.images.length > 0 && (typeof this.images[0] == 'string');
    }

    onShowAllClick = () => {
        this.count = this.images.length;
    }
}
