  setDisplayTemplate(): void {
    if ((this.field.type == FieldType.RADIO || this.field.type == FieldType.CHECKBOX) && StringUtils.isEmpty((<RadioField | CheckboxField>this.field).displayTemplate)) {
      (<RadioField | CheckboxField>this.field).displayTemplate = OptionDisplayTemplate.CLASSIC;
    }
  }

  setAddMoreDisplayInColumn(): void {
    if (this.isAddMore) {
      if (this.device == 'desktop') {
        if (this.displayInColumns < 6) {
          this.addMoreFieldDisplayInColumns = 10;
          this.addMoreButtonDisplayInColumns = 2;
        } else {
          this.addMoreFieldDisplayInColumns = 11;
          this.addMoreButtonDisplayInColumns = 1;
        }
      }

      if (this.device == 'tablet') {
        if (this.displayInColumns < 6) {
          this.addMoreFieldDisplayInColumns = 6;
          this.addMoreButtonDisplayInColumns = 2;
        } else {
          this.addMoreFieldDisplayInColumns = 7;
          this.addMoreButtonDisplayInColumns = 1;
        }
      }

      if (this.device == 'mobile') {
        if (this.displayInColumns < 3) {
          this.addMoreFieldDisplayInColumns = 3;
          this.addMoreButtonDisplayInColumns = 2;
        } else {
          this.addMoreFieldDisplayInColumns = 3;
          this.addMoreButtonDisplayInColumns = 1;
        }
      }

    }
  }

  setMinLengthForTrigger(): void {
    this.minLengthForTrigger = (<AutocompleteField>this.field).minLengthForTrigger ? (<AutocompleteField>this.field).minLengthForTrigger : 3;
  }

  setRows(): void {
    this.rows = (<TextField>this.field).rows ? (<TextField>this.field).rows : 1;
  }

  setHeight(): void {
    this.height = (<HtmlEditorField>this.field).height ? (<HtmlEditorField>this.field).height : 300;
  }

  setAppearance(): void {
    this.appearance = (<TextField | InputField>this.field).appearance ? (<TextField | InputField>this.field).appearance : 'standard';
  }

  setAsBubble(): void {
    this.asBubble = (<LabelField>this.field).asBubble ? (<LabelField>this.field).asBubble : false;
    this.bubbleColor = (<LabelField>this.field).bubbleColor ? (<LabelField>this.field).bubbleColor : ButtonColor.DEFAULT;
  }

  setTextAlign(): void {
    this.textAlign = (<LabelField>this.field).textAlign ? (<LabelField>this.field).textAlign : LabelTextAlign.LEFT;
  }

