  required(): boolean {
    let isRequired: boolean = false;
    if (this.field.validations && this.field.validations.length > 0) {
      for (let validation of this.field.validations) {
        if (validation.message.key == "required") {
          isRequired = true;
        }
      }
    }

    if (StringUtils.isEmpty(this.field.label)) {
      isRequired = false;
    }

    return isRequired;
  }

  fieldNavigate(): boolean {
    let hasFieldNavigate: boolean = false;

    if (
      this.field.navigate &&
      (!StringUtils.isEmpty(this.field.navigate.icon) || !StringUtils.isEmpty(this.field.navigate.text)) &&
      !CollectionUtils.isEmpty(this.field.navigate.routeTo)
    ) {
      hasFieldNavigate = true;
    }

    return hasFieldNavigate;
  }

  fieldDisabled(): boolean {
    let isDisabled: boolean = false;

    if (this.field.alwaysEnable) {} else {
      isDisabled = FieldUtils.isFieldDisabled(this.field, this._displayMode, this.resolvedValue);
      if (isDisabled) {
        FormUtils.disableFieldControl(<FormControl>this.fieldControl);
      }
    }

    return isDisabled;
  }

  getValue(field: Field, txt: any): any {
    let value: string = "";
    let initialLength = 0;
    let finalLength = 0;

    if (this.fieldControl != undefined) {
      value = this.fieldControl.value;
    } else {
      value = txt;
    }

    if (field.type != FieldType.DROPDOWN) {
      if (this.keyMap && this.keyMap.length > 0) {
        this.keyMap
          .filter(objs => {
            let hasAssociation: boolean = false;
            if (!CollectionUtils.isEmpty(objs) && !CollectionUtils.isEmpty(objs.associations)) {
              objs.associations.forEach(element => {
                if (element.fieldKey == field.key && element.componentIdentifier == this.sourceIdentifier) {
                  hasAssociation = true;
                }
              });
            }
            return hasAssociation;
          })
          .forEach(field => {
            if (field['options'] && field['options'].length > 0) {
              for (let index = 0; index < field['options'].length; index++) {
                if (field['options'][index].key == value) {
                  value = field['options'][index].value;
                  break;
                }
              }
            }
          });
      }
    }

    this.resolvedValue = value;

    if (FieldUtils.isEllipsisField(this.field) && FieldUtils.displayEllipsis(this.charLimit, this.resolvedValue)) {
      value = JSON.stringify(value);
      initialLength = value.length;

      value = value.substr(0, (<TextField>this.field).ellipsis);
      finalLength = value.length;

      value += "...";

      if (initialLength > finalLength) {
        this.ellipsisRequired = true;
      }
    }

    return value;
  }

  displayFullText() {
    let resolvedValueStr = JSON.stringify(this.resolvedValue);
    if (!StringUtils.isEmpty(resolvedValueStr)) {
      this.charLimit = resolvedValueStr.length;
    }
  }

  displayShortText() {
    if ((<TextField>this.field).ellipsis && (<TextField>this.field).ellipsis > 0) {
      this.charLimit = (<TextField>this.field).ellipsis;
      this.displayEllipsis();
    }
  }

  displayEllipsis(): boolean {
    return FieldUtils.displayEllipsis(this.charLimit, this.resolvedValue);
  }

  setCalendar() {
    if (this.field.type === FieldType.CALENDAR) {
      this.dateRange = (<CalendarField>this.field).dateRange;
    }
  }

  ///////////////////////////// AUTOCOMPLETE - START ///////////////////////////
  private invokeOnChangeOnField(value: string, input: any): boolean {
    let invokeOnChange: boolean = false;

    if (this.field.type == FieldType.AUTOCOMPLETE) {
      this.autoCompleteInputHasValue = !!value;

      if (value.length >= this.minLengthForTrigger) {
        this.autoCompleteInputHasValue = !!value;
        this.highlightAutoCompleteText = value;
      } else {
        this.autoCompleteInputHasValue = false;
        this.highlightAutoCompleteText = '';
      }
      invokeOnChange = true;
    } else {
      invokeOnChange = true;
    }

    return invokeOnChange;
  }

  autocompleteDisplayFn(option: DropdownOption | any): string {
    let value: string;
    if (option != null && option.key != null) {
      value = option.value;
    } else {
      value = option;
    }

    return value;
  }

  private _filter(value: string): Array<DropdownOption> {
    const filterValue = value.toLowerCase();
    var result = (<AutocompleteField>this.field).options.filter(option => option.value.toLowerCase().includes(filterValue));
    this.autoCompleteResultCount = result.length;
    return result;
  }
  ///////////////////////////// AUTOCOMPLETE - END ///////////////////////////

  ///////////////////////////// DROPDOWN OPTION - START ///////////////////////////
  setOptionInDisplay(): void {
    if (this.field['options'] && this.fieldControl) {
      let key: any = this.fieldControl.value;

      for (let cnt = 0; cnt < (this.field['options'] as Array<DropdownOption>).length; cnt++) {
        if (this.field['options'][cnt].key === key) {
          this.field['options'][cnt].checked = true;
        }
      }
    }
  }


  setClear() {
    let hasClear = false;
    if (this.resolvedValue && (<TextField>this.field).hasClear && (
      this.field.type == FieldType.TEXT ||
      this.field.type == FieldType.TEXTAREA ||
      this.field.type == FieldType.EMAIL ||
      this.field.type == FieldType.PASSWORD ||
      this.field.type == FieldType.COLOR ||
      this.field.type == FieldType.MONTH ||
      this.field.type == FieldType.WEEK ||
      this.field.type == FieldType.NUMBER ||
      this.field.type == FieldType.DROPDOWN ||
      this.field.type == FieldType.AUTOCOMPLETE ||
      this.field.type == FieldType.CHIPS ||
      this.field.type == FieldType.TIME ||
      this.field.type == FieldType.CALENDAR
    )) {
      hasClear = true;
    }

    this.hasClear = hasClear;
  }

  cleanValue() {
    this.resolvedValue = "";
    this.fieldControl.setValue("");
    this.setClear();
  }

  getResolveTextWithKey(field: any) {
    let value: string = "";

    if (this.row != undefined) {
      if (this.row[field['resolveOptionWithKey']]) {
        value = this.row[field['resolveOptionWithKey']];
      }
    }

    return value;
  }

  getDropdownValue(field: Field, txt: any): any {
    let value: any = "";

    if (this.fieldControl != undefined) {
      if (this.fieldControl.value) {
        value = this.fieldControl.value;
      }
    } else {
      value = txt;
    }

    if (field['options'] && field['options'].length > 0) {
      for (let index = 0; index < field['options'].length; index++) {
        let isSelected: boolean = false;
        if (value instanceof Array) {
          if (value.indexOf(field['options'][index].key) > -1) {
            isSelected = true;
          }
        } else if (value instanceof String) {
          if (field['options'][index].key == value) {
            isSelected = true;
          }
        } else if (value instanceof Object) {
          if (value[field['options'][index].key]) {
            isSelected = true;
          }
        }

        if (isSelected) {
          value = field['options'][index].value;
          break;
        }
      }
    }


    return value;
  }
