import { KeyMap, KeyMapAssociation, DropdownOption, KeyMapOptionType } from '../field/model';
import { CollectionUtils } from './collection.utility';
import { FormUtils } from './form.utility';
import { ListUtils } from './list.utility';
import { CrudUtils } from './crud.utility';
import { MasterDataUtils } from './master-data.utility';
import { Crud } from '../crud/model';
import { Form } from '../form/model';
import { List } from '../list/model';

export class KeyMapUtils {
  static push(keyMaps: Array<KeyMap>, associations: Array<KeyMapAssociation>, options: Array<DropdownOption> | string, optionType: KeyMapOptionType, optionComponent: Crud | Form | List, optionDependsOnValue?: string, record?: any): Array<KeyMap> {
    // if (CollectionUtils.isEmpty(keyMaps)) {
    //   keyMaps = new Array<KeyMap>();
    // }

    let optionsDD: Array<DropdownOption>;
    if (!(options instanceof Array)) {
      optionsDD = MasterDataUtils.getMasterDataAsOptions(<string>options);
    } else {
      optionsDD = options;
    }

    if (!CollectionUtils.isEmpty(associations)) {
      keyMaps.push(KeyMapUtils.generateKeyMap(associations, options, optionDependsOnValue));

      KeyMapUtils.setOptionssUsingValues(keyMaps, true, optionType, optionComponent, record);
    }

    return keyMaps;
  }

  static generateKeyMap(associations: Array<KeyMapAssociation>, options: Array<DropdownOption> | string, optionDependsOnValue?: string): KeyMap {
    // if (CollectionUtils.isEmpty(keyMaps)) {
    //   keyMaps = new Array<KeyMap>();
    // }

    let optionsDD: Array<DropdownOption>;
    if (!(options instanceof Array)) {
      optionsDD = MasterDataUtils.getMasterDataAsOptions(<string>options);
    } else {
      optionsDD = options;
    }

    let keyMap: KeyMap;
    if (!CollectionUtils.isEmpty(associations)) {
      keyMap = { associations: associations, options: optionsDD, optionDependsOnValue: optionDependsOnValue };
    }

    return keyMap;
  }

  static setOptionssUsingValues(keyMaps: Array<KeyMap>, relaodAll: boolean, optionType: KeyMapOptionType, optionComponent: Crud | Form | List, record?: any): void {
    if (!CollectionUtils.isEmpty(keyMaps)) {
      keyMaps.forEach(keyMap => {
        if (!CollectionUtils.isEmpty(keyMap.associations)) {
          keyMap.associations.forEach(association => {
            if (optionType != null && KeyMapOptionType.CRUD == optionType) {
              CrudUtils.setOptionsUsingValues(<Crud>optionComponent, association.fieldKey, keyMap, relaodAll, record);
            }
            if (KeyMapOptionType.FORM == optionType) {
              FormUtils.setOptionsUsingValues(<Form>optionComponent, association.fieldKey, keyMap, relaodAll, record);
            }
            if (KeyMapOptionType.LIST == optionType) {
              ListUtils.setOptionsUsingValues(<List>optionComponent, association.fieldKey, keyMap, relaodAll, record);
            }
          });
        }
      })
    }
  }

  static getValue(keyMaps: Array<KeyMap>, fieldKey: string, valueAsKey: any): any {
    let value: any = "";

    if (!CollectionUtils.isEmpty(keyMaps)) {
      keyMaps.forEach(keyMap => {
        if (!CollectionUtils.isEmpty(keyMap.associations)) {
          keyMap.associations.forEach(association => {
            if (association.fieldKey == fieldKey && !CollectionUtils.isEmpty(keyMap.options)) {
              keyMap.options.forEach(option => {
                if (option.key == valueAsKey) {
                  value = option.value;
                }
              });
            }
          })
        }
      });
    }

    return value;
  }
}
