import { BreakpointObserver, Breakpoints } from "@angular/cdk/layout";
import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
} from "@angular/core";
import { MatSidenav } from "@angular/material/sidenav";
import { Navigation, NavigationPannel } from "../../model";

@Component({
  selector: "admin-navigation",
  templateUrl: "./navigation.component.html",
  styleUrls: ["./navigation.component.scss"],
})
export class NavigationComponent implements OnInit {
  @ViewChild("sidenav") sidenav: MatSidenav;
  @Input() isExpanded = true;
  @Input() navigationPanel: NavigationPannel;
  @Input() navigation: Array<Navigation>;
  @Output() logout = new EventEmitter<any>();
  showSubmenu: Array<boolean> = [false];
  isShowing = false;
  showSubSubMenu: Array<any> = [[false, false], false];

  constructor(
    public breakpointObserver: BreakpointObserver,
    private ref: ChangeDetectorRef
  ) {}

  ngAfterViewChecked(): void {
    //Called after every check of the component's view. Applies to components only.
    //Add 'implements AfterViewChecked' to the class.
    this.ref.detectChanges();
  }

  mouseenter() {
    if (!this.isExpanded) {
      this.isShowing = true;
    }
  }

  mouseleave() {
    if (!this.isExpanded) {
      this.isShowing = false;
    }
  }

  logoutEvent() {
    this.logout.emit();
  }

  ngOnInit() {}

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    this.navigation = this.navigationPanel.navigations;
    this.isExpanded = (!this.navigationPanel.defaultCollapse);
    this.breakpointObserver
      .observe([Breakpoints.HandsetLandscape, Breakpoints.HandsetPortrait])
      .subscribe((result) => {
        if (result.matches) {
          this.isExpanded = false;
        }
      });
  }
}
