import { Component, OnInit } from '@angular/core';
import { CrudHeader, ActionPage, CrudList, CrudListData, Crud, CrudSearch, CrudTab } from '../../model';
import { Button, Badge, Action } from '../../../button/model';
import { KeyMap } from '../../../field/model';
import { List } from '../../../list/model';
import { CollectionUtils, SecurityUtils, CrudUtils } from '../../../utility';
import { FormDiaplyMode } from '../../../form/model';

//extends ModalInterfaceComponent
@Component({
  selector: 'mx-crud-list-interface',
  templateUrl: './crud-list.component.html',
  styleUrls: ['./crud-list.component.scss']
})
export class CrudListComponentInterface  implements OnInit {
  //  Common Config 
  identifier: string;
  header: CrudHeader;
  actions: Array<Button>;
  actionPages: Array<ActionPage>;
  formReset: boolean;
  listReset: boolean;
  keyMap: Array<KeyMap> = new Array<KeyMap>();

  //  List Config
  quickLinks: Array<Button>;
  searchConfig: CrudSearch;
  searchData: any;
  listConfig: CrudList;
  listPageBackRoute: Array<string>;

  expandRowIndex: number;
  
  configListData: CrudListData; 
  originalData: any;

  constructor() { 
    // super();
  }

  ngOnInit(): void {
    this.setCommonConfig();
    this.setListConfig();
  }

  setCommonConfig(): void {

  }
  setListConfig(): void{

  }
  setBadges(): Array<Badge>{
    return null;
  }

  setCommonConfigUsingCrud(crud: Crud): void {
    //  Config
    this.identifier = crud.identifier
    this.header = crud.header;
    
    //  Tab wise data/setting
    //this.keyMap = {};
    this.listReset = true;
  }

  setListConfigUsingCrud(crud: Crud): void {
    //  Config
    this.quickLinks = crud.quickLinks;
    this.searchConfig = crud.search;
    this.listConfig = crud.list;
    this.actions = crud.actions;

    this.setConfigListData([], []);
  }
 
  loadFilterParams(filterStr: string) {
    if (filterStr != null) {
      const filter = SecurityUtils.decrypt(filterStr);

      this.configListData.searchData = filter;
    } else {
    }
    this.configListData.pageNo = 1;
  }
  
  setConfigListData(records: Array<any>, badges: Array<Badge>, route?: Array<string>): void {
    let configListDataNew = {
      pageBackRoute: route, 
      badges: badges,
      records: records,
      originalData: this.originalData
    }

    this.configListData = { ...this.configListData, ...configListDataNew };    
  }

  getRowKey(lIndex: number): Array<string> {
    let keys: Array<string>;

    if (this.listConfig && this.listConfig.lists && this.listConfig.lists[lIndex]) {
      keys = this.listConfig.lists[lIndex].uniqueKeys;
    } else {
      keys = new Array<string>();
    }

    return keys;
  }

  getChildRowKey(lIndex: number): Array<string> {
    let keys: Array<string>;

    if (this.listConfig && this.listConfig.lists && this.listConfig.lists[lIndex] && this.listConfig.lists[lIndex].child && this.listConfig.lists[lIndex].child.record && (<List>this.listConfig.lists[lIndex].child.record).uniqueKeys) {
      keys = (<List>this.listConfig.lists[lIndex].child.record).uniqueKeys;
    } else {
      keys = new Array<string>();
    }

    return keys;
  }

  getChildRecordIdentifier(lIndex: number): string {
    let childRecordIdentifier: string;

    if (this.listConfig && this.listConfig.lists && this.listConfig.lists[lIndex] && this.listConfig.lists[lIndex].child) {
      childRecordIdentifier = this.listConfig.lists[lIndex].child.recordIdentifier;
    } else {
      childRecordIdentifier = "";
    }

    return childRecordIdentifier;
  }

  beforeChangeMerge(action: Action, sourceIdentifier: string): { rows: Array<any>, rowIndex: number } {
    let rows: Array<any>;
    let rowIndex: number = -1;

    if (this.configListData && this.configListData.records) {
      for (let lIndex = 0; lIndex < this.configListData.records.length; lIndex++) {
        if (this.listConfig.lists[lIndex].identifier == sourceIdentifier) {
          for (let rIndex = 0; rIndex < this.configListData.records[lIndex].rows.length; rIndex++) {
            let keys = this.getRowKey(lIndex);

            let isMatchingRow = true;
            for (let key of keys) {
              if (this.configListData.records[lIndex].rows[rIndex][key] != action.originalData[key]) {
                isMatchingRow = false;
              }
            }

            if (isMatchingRow) {
              this.configListData.records[lIndex].rows[rIndex] = { ...action.originalData, ...action.data };

              rows = this.configListData.records[lIndex].rows;
              rowIndex = rIndex;
              break;
            }
          }
        }
      }
    }

    return { rows: rows, rowIndex: rowIndex };
  }

  addRow(action: Action, sourceIdentifier: string, data?: any): { rows: Array<any>, rowIndex: number } {
    let rows: Array<any>;
    let rowIndex: number = -1; 

    if (this.configListData && this.configListData.records) {
      for (let lIndex = 0; lIndex < this.configListData.records.length; lIndex++) {
        if (this.listConfig.lists[lIndex].identifier == sourceIdentifier) {
          if (CollectionUtils.isEmpty(data))  {
            this.configListData.records[lIndex].rows.push({});
          } else  {
            this.configListData.records[lIndex].rows.push(data);
          }

          rows = this.configListData.records[lIndex].rows;
          rowIndex = this.configListData.records[lIndex].rows.length - 1;
          break;
        }
      }
    }

    return { rows: rows, rowIndex: rowIndex };
  }

  afterChangeMerge() {
    this.configListData = JSON.parse(JSON.stringify(this.configListData));
    this.listReset = true;
  }


  beforeChildChangeMerge(action: Action, sourceIdentifier: string): { rows: Array<any>, rowIndex: number } {
    let rows: Array<any>;
    let rowIndex: number = -1;

    if (this.configListData && this.configListData.records) {
      for (let lIndex = 0; lIndex < this.configListData.records.length; lIndex++) {
        if (this.listConfig.lists[lIndex].identifier == sourceIdentifier) {
          for (let rIndex = 0; rIndex < this.configListData.records[lIndex].rows.length; rIndex++) {
            let keys = this.getRowKey(lIndex);

            let isMatchingRow = true;
            for (let key of keys) {
              if ((<Array<string>>action.parentHierarchy.parent['key']).indexOf(this.configListData.records[lIndex].rows[rIndex][key]) < -1) {
                isMatchingRow = false;
              }
            }

            if (isMatchingRow) {
              let childRecordIdentifier = this.getChildRecordIdentifier(lIndex);

              for (let cIndex = 0; cIndex < this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier].length; cIndex++) {
                let keys = this.getChildRowKey(lIndex);

                let isChildMatchingRow = true;
                for (let key of keys) {
                  if (this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier][cIndex][key] != action.originalData[key]) {
                    isChildMatchingRow = false;
                  }
                }

                if (isChildMatchingRow) {
                  this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier][cIndex] = { ...action.originalData, ...action.data };

                  rows = this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier];
                  rowIndex = cIndex;
                  break;
                }
              }
            }
          }
        }
      }
    }

    return { rows: rows, rowIndex: rowIndex };
  }

  addChildRow(action: Action, sourceIdentifier: string, data?: any): { rows: Array<any>, rowIndex: number } {
    let rows: Array<any>;
    let rowIndex: number = -1;

    if (this.configListData && this.configListData.records) {
      for (let lIndex = 0; lIndex < this.configListData.records.length; lIndex++) {
        if (this.listConfig.lists[lIndex].identifier == action.sourceIdentifier) {
          for (let rIndex = 0; rIndex < this.configListData.records[lIndex].rows.length; rIndex++) {
            let keys = this.getRowKey(lIndex);

            let isMatchingRow = true;
            for (let key of keys) {
              if ((<Array<string>>action.originalData[key]).indexOf(this.configListData.records[lIndex].rows[rIndex][key]) < -1) {
                isMatchingRow = false;
              }
            }

            if (isMatchingRow) {
              let childRecordIdentifier = this.getChildRecordIdentifier(lIndex);

              for (let cIndex = 0; cIndex < this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier].length; cIndex++) {
                if (CollectionUtils.isEmpty(data))  {
                  this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier].push({});
                } else  {
                  this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier].push(data);
                }

                rows = this.configListData.records[lIndex].rows[rIndex][childRecordIdentifier];
                rowIndex = cIndex;
                break;
              }
            }
          } 
        }
      }
    }

    return { rows: rows, rowIndex: rowIndex };
  }

  setTabDisplayMode(crudTabs: Array<CrudTab>, formDisplayMode: FormDiaplyMode) {
    CrudUtils.setDisplayType(crudTabs, formDisplayMode);
  }

  afterChildChangeMerge() {
    this.afterChangeMerge();
  }

  setHeaderTitle(title: string)  {
    this.header.title = title;
  }

  setHeaderDescription(description: string)  {
    this.header.description.text = description;
  }
}
