import { CollectionUtils } from './collection.utility';
import { DropdownOption } from '../field/model';
import { MasterData } from '../setting/model';

export class MasterDataUtils {
    static getMasterDataFromLocalStore(key: string): MasterData {
        let masterDataMap: Map<string, MasterData> = JSON.parse(localStorage.getItem("masterData"));

        return masterDataMap[key];
    }

    static getMasterDataAsOptions(masterDataKey: string): Array<DropdownOption> {
        let masterData = MasterDataUtils.getMasterDataFromLocalStore(masterDataKey);

        let options: Array<DropdownOption> = new Array<DropdownOption>();

        if (masterData && !CollectionUtils.isEmpty(masterData)) {
            masterData.items.forEach(record => {
                options.push({ key: record.key, value: record.label });
            });
        }

        return options;
    }
}
