import { CellControl, CellControllType, Column, List, ListCustomLayout } from '../list/model';
import { CollectionUtils } from './collection.utility';
import { FormUtils } from './form.utility';
import { FieldUtils } from './field.utility';
import { StringUtils } from './string.utility';
import { FieldType, AutocompleteField, RadioField, CheckboxField, DropdownField, DropdownOption, KeyMap, FieldAppearance, FieldDiaplyType } from '../field/model';
import { Button, ButtonType } from '../button/model';

export class ListUtils {
    static addColoumn() {

    }

    static hideColoumn() {

    }

    static setOptionsUsingKey(list: List, fieldKey: string, masterDataKey: string) {
        if (!CollectionUtils.isEmpty(list) && !CollectionUtils.isEmpty(list.columns)) {
            list.columns.forEach(column => {
                if (!CollectionUtils.isEmpty(column) && !CollectionUtils.isEmpty(column.fields)) {
                    column.fields.forEach(field => {
                        if (!CollectionUtils.isEmpty(field) && field.key == fieldKey && (field.type == FieldType.AUTOCOMPLETE || field.type == FieldType.CHECKBOX || field.type == FieldType.RADIO || field.type == FieldType.DROPDOWN)) {
                            FieldUtils.setOptionsUsingKey(<AutocompleteField | RadioField | CheckboxField | DropdownField>field, masterDataKey);
                        }
                    })
                }
            })
        }
    }

    static setOptionsUsingValues(list: List, fieldKey: string, keyMap: KeyMap, relaodAll: boolean, record: any) {
        if (!CollectionUtils.isEmpty(list) && !CollectionUtils.isEmpty(list.columns)) {
            list.columns.forEach(column => {
                if (!CollectionUtils.isEmpty(column) && !CollectionUtils.isEmpty(column.fields)) {
                    column.fields.forEach(field => {
                        let isMatchingDependendKeyMap: boolean = false;
                        if (!StringUtils.isEmpty((<AutocompleteField | RadioField | CheckboxField | DropdownField>field).optionDependsOn)) {
                            //  get optionDependsOn field value
                            let optionDependsOnValue = FormUtils.getValueViaEval(record, (<AutocompleteField | RadioField | CheckboxField | DropdownField>field).optionDependsOn);

                            if (keyMap.optionDependsOnValue == optionDependsOnValue) {
                                isMatchingDependendKeyMap = true;
                            }
                        } else {
                            isMatchingDependendKeyMap = true;
                        }

                        if (!CollectionUtils.isEmpty(field) && field.key == fieldKey && (field.type == FieldType.AUTOCOMPLETE || field.type == FieldType.CHECKBOX || field.type == FieldType.RADIO || field.type == FieldType.DROPDOWN)) {
                            if (isMatchingDependendKeyMap) {//} || relaodAll)   {
                                FieldUtils.setOptionsUsingValues(<AutocompleteField | RadioField | CheckboxField | DropdownField>field, keyMap);
                            }
                        }
                    })
                }
            })
        }
    }


    static getColumnKey(column: Column): string {
        let key;
        if (column.key) {
            key = column.key;
        } else {
            if (column.fields && column.fields.length > 0) {
                key = column.fields[0].key;
            }
        }

        return key;
    }


    static getColumnLabel(column: Column): string {
        let label;
        if (column.label) {
            label = column.label;
        } else {
            if (column.fields && column.fields.length > 0) {
                label = column.fields[0].label;
            }
        }

        return label;
    }

    static getColumnSelectorField(listConfig: List): DropdownField {
        let options: Array<DropdownOption> = new Array<DropdownOption>();

        let values: Array<string> = new Array<string>();
        for (let column of listConfig.columns) {
            let key: string = this.getColumnKey(column);
            let label: string = this.getColumnLabel(column);

            options.push({
                key: key,
                value: label,
                disabled: false,
                selected: column.show
            });

            if (column.show) {
                values.push(key)
            }
        }

        let columnSelectorField: DropdownField = {
            key: "columnSelector",
            label: "Display Column",
            type: FieldType.DROPDOWN,
            appearance: FieldAppearance.STANDARD,
            isReadOnly: false,
            fieldDisplayType: FieldDiaplyType.INLINE,
            placeholder: "Columns to display",
            options: options,
            multiselect: true,
            value: values
        };

        return columnSelectorField;
    }

    static getMobileConfig(listConfig: List) {
        if (!CollectionUtils.isEmpty(listConfig) && !CollectionUtils.isEmpty(listConfig.mobile) && !CollectionUtils.isEmpty(listConfig.mobile.cells)) {
            this.setCustomLayout(listConfig, listConfig.mobile);
        }
    }

    static setCustomLayouts(listConfig: List) {
        if (this.hasRowHover(listConfig)) {
            this.setCustomLayout(listConfig, listConfig.row.hover.template.layout);
        }
        if (this.hasCustomCellLayout(listConfig)) {
            listConfig.columns.forEach(column => {
                if (column.template && column.template.layout)  {
                    this.setCustomLayout(listConfig, column.template.layout);
                }
            })
        }
        if (this.hasCustomRowLayout(listConfig)) {
            this.setCustomLayout(listConfig, listConfig.row.template.layout);
        }
    }

    static hasRowHover(listConfig: List): boolean {
        let hasHoverConfig: boolean = false;
        
        if (listConfig && listConfig.row && listConfig.row.hover && listConfig.row.hover.template && listConfig.row.hover.template.layout)  {
            hasHoverConfig = true;
        }

        return hasHoverConfig;
    }

    static hasCustomRowLayout(listConfig: List): boolean {
        let hasCustomRowLayout: boolean = false;
        
        if (listConfig && listConfig.row && listConfig.row.template && listConfig.row.template.layout)  {
            hasCustomRowLayout = true;
        }
        
        return hasCustomRowLayout;
    }

    static hasCustomCellLayout(listConfig: List): boolean {
        let hasCustomCellLayout: boolean = false;
        
        if (listConfig && !CollectionUtils.isEmpty(listConfig.columns))  {
            listConfig.columns.forEach(column => {
                if (column.template && column.template.layout)  {
                    hasCustomCellLayout = true;
                }
            })
        }
        
        return hasCustomCellLayout;
    }

    static setCustomLayout(listConfig: List, layout: ListCustomLayout) {
        if (layout && !CollectionUtils.isEmpty(layout.cells)) {
            layout.cells.forEach(cell => {
                if (!CollectionUtils.isEmpty(cell.controls)) {
                    cell.controls.forEach(control => {
                        if (CollectionUtils.isEmpty(control.control))   {
                            let resolvedControl: { control?: any, colIndex?: number, cControlIndex?: number } = {};
                            resolvedControl = this.getControl(listConfig, control);

                            control.control = resolvedControl.control;
                            control.colIndex = resolvedControl.colIndex;
                            control.cControlIndex = resolvedControl.cControlIndex;

                            // if (control.type == CellControllType.BUTTON && control.control['type'] == ButtonType.CHIP) {
                            //     control.control['groupIdentifier'] = control.control.identifier + cell.rows + cell.cols;
                            // }
                        }
                    });
                }
            })
        }
    }

    static getControl(listConfig: List, cellControl: CellControl): { control?: any, colIndex?: number, cControlIndex?: number } {
        let control: { control?: any, colIndex?: number, cControlIndex?: number } = {};

        if (cellControl.control) {
            control = { control: cellControl.control, colIndex: 0, cControlIndex: 0 };
        } else if (!CollectionUtils.isEmpty(listConfig) && !CollectionUtils.isEmpty(listConfig.columns) && cellControl.type == CellControllType.FIELD) {
            control = this.getColumnControl(listConfig.columns, cellControl.key);
        } else if (!CollectionUtils.isEmpty(listConfig) && !CollectionUtils.isEmpty(listConfig.actions) && cellControl.type == CellControllType.BUTTON) {
            control.control = this.getButtonControl(listConfig.actions, cellControl.key);
            control.colIndex = listConfig.columns.length;
        }

        return control;
    }

    static getColumnControl(columns: Array<Column>, controlKey: string): { control?: any, colIndex?: number, cControlIndex?: number } {
        let control: { control?: any, colIndex?: number, cControlIndex?: number } = {};

        let colIndex = 0;
        columns.forEach(column => {
            if (!CollectionUtils.isEmpty(column.fields) && CollectionUtils.isEmpty(control.control)) {
                let cControlIndex = 0;
                column.fields.forEach(field => {
                    if (CollectionUtils.isEmpty(control.control)) {
                        control = ListUtils.getFieldControl(field, controlKey, colIndex, cControlIndex);
                        cControlIndex++;
                    }
                })
            }
            colIndex++;
        })

        return control;
    }

    static getFieldControl(field: any, controlKey: string, colIndex: number, cControlIndex: number): { control?: any, colIndex?: number, cControlIndex?: number } {
        let control: { control?: any, colIndex?: number, cControlIndex?: number } = {};

        if (field.key == controlKey) {
            control.control = field;
            control.colIndex = colIndex;
            control.cControlIndex = cControlIndex;
            console.log("1111111");
            console.log(control);
        }

        return control;
    }

    static getButtonControl(buttons: Array<Button>, controlKey: string): any {
        let control: any;

        buttons.forEach(button => {
            if (button.identifier == controlKey) {
                control = button;
            }
        })

        return control;
    }
}