import { DropdownOption } from '../field/model';
import { CollectionUtils } from './collection.utility';

export class DropdownUtils {
  static getValue(key: string, options: Array<DropdownOption>): any  {
    let value: any;

    if (!CollectionUtils.isEmpty(options))  {
      options.forEach(option => {
        if (option.key == key)  {
          value = option.value;
        }
      })
    }

    return value;
  }
}
