import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Button, ObjectTree, ReservedButton, Action, ButtonType, HoverButton, ButtonGroup, IconPosition } from '../../model';
import { FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
//import { ButtonDeleteConfirmationComponent } from '../button-delete-confirmation/button-delete-confirmation.component';
import { Subscription } from 'rxjs';
import { Params, Router, ActivatedRoute } from '@angular/router';
import { FormDiaplyMode } from '../../../form/model';
import { StringUtils, CollectionUtils, FormUtils, SecurityUtils, ButtonUtils, DependentUtils } from '../../../utility';
import { AbilityUtils } from '../../../utility/ability.utility';
import { Ability } from '@casl/ability';
import { ButtonDeleteConfirmationComponent } from '../button-delete-confirmation/button-delete-confirmation.component';

//export { Button, ObjectTree, ReservedButton, Action, ButtonType, HoverButton, ButtonGroup, IconPosition } from '../../model';

@Component({
  selector: 'mx-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss']
})
export class ButtonComponent implements OnInit, OnDestroy {
  @Input() form: FormGroup;
  @Input() formDisplayMode: FormDiaplyMode;

  @Input() sourceIdentifier: string;
  @Input() sourceIndex: number;
  @Input() widgetArrayIndex: number;
  @Input() identifier: string;
  @Input() context: any;
  @Input() originalData: any;
  @Input() label: string;
  @Input() buttonRoute: Array<string>;
  @Input() parentHierarchy: ObjectTree;
  @Input() disabled: boolean;
  @Output() onClick = new EventEmitter();
  @Output() onIconClick = new EventEmitter();

  @Input() _button: Button | ButtonGroup | HoverButton;
  get button(): Button | ButtonGroup | HoverButton {
    return this._button;
  }
  @Input()
  set button(_button: Button | ButtonGroup | HoverButton) {
    this._button = _button;
    if (StringUtils.isEmpty(this.label) || !StringUtils.isEmpty(this._button.label)) {
      this.label = this._button.label;
    }
    if (StringUtils.isEmpty(this._button.width)) {
      this._button.width = '';
    } else {
      this._button.width = this._button.width + '%';
      document.documentElement.style.setProperty('--width', this._button.width + '%');
    }
    if (StringUtils.isEmpty(this._button.iconPosition)) {
      this._button.iconPosition = IconPosition.LEFT;
    }
  }

  routerLink = [];

  displayButton: boolean = true;
  isButtonDisabled: boolean = false;
  isDisplayOnlyIcon: boolean = false;
  hasIconDisplay: boolean = false;
  isFlatButton: boolean = false;
  isGhostButton: boolean = false;
  isRaisedButton: boolean = false;
  isStrokedButton: boolean = false;
  isFabButton: boolean = false;
  isChip: boolean = false;
  isLink: boolean = false;
  isGroupButton: boolean = false;
  isMenuButton: boolean = false;
  buttonWithoutPadding: boolean = false;


  formSubscription: Subscription;

  constructor(
    public dialog: MatDialog,
    private router: Router,
    private route: ActivatedRoute,
    private ability: Ability
  ) {
    AbilityUtils.setAbility(this.ability);
  }

  ngOnInit() {
    if (this.displayButton) {
      this.setLabel();

      this.setButtonEnable();
      if (this.form) {
        this.formSubscription = this.form.valueChanges.subscribe(obj => {
          this.setButtonEnable();
        });
      }

      this.setButtonDisplay();
      this.setOnlyIconDisplay();
      this.setIconDisplay();
      this.setLink();
      this.setGroupButton();
      this.setFlatButton();
      this.setGhostButton();
      this.setRaisedButton();
      this.setStrokedButton();
      this.setFabButton();
      this.setChip();
      this.setButtonWithoutPadding();
      this.setMenuButton();
    }
  }

  setButtonDisplay() {
    if ((!CollectionUtils.isEmpty(this._button.displayInFormModes) && this._button.displayInFormModes.indexOf(this.formDisplayMode) <= -1) || !DependentUtils.displayDependencyField(this._button.dependentOnFields, null, this.context ? this.context : this.originalData)) {
      this.displayButton = false;
    }
  }

  setButtonWithoutPadding() {
    if (this._button.identifier == "fieldTextClearBtn" || this._button.identifier == "fieldRouteToBtn" || this._button.identifier == "listFieldAsLink" || this.identifier == "listFieldAsLink") {
      this.buttonWithoutPadding = true;
    }
  }
  setLabel(): void {
    if (StringUtils.isEmpty(this.label)) {
      this.label = this._button.label;
    }
  }

  state: boolean = true;
  click(event: any) {
    if (!CollectionUtils.isEmpty(this._button.confirmationConfiguration)) {
      this.openDialog(event);
    } else {
      this.invokeAction(null, event);
    }
  }

  openDialog(event: any): void {
    const dialogRef = this.dialog.open(ButtonDeleteConfirmationComponent, {
      width: this._button.confirmationConfiguration.width ? this._button.confirmationConfiguration.width : '250px',
      data: {
        form: this.form,
        sourceIdentifier: this.sourceIdentifier,
        sourceIndex: this.sourceIndex,
        widgetArrayIndex: this.widgetArrayIndex,
        context: this.context,
        originalData: this.originalData,
        buttonRoute: this.buttonRoute,
        parentHierarchy: this.parentHierarchy,
        confirmationConfig: this._button.confirmationConfiguration,
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.invokeAction(result, event);
    });
  }

  invokeAction(actionDialog: any, event: any): void {
    if (this._button.identifier == "crudBackButton" && this.buttonRoute && this.buttonRoute.length > 0) {
      this.router.navigate(this.buttonRoute, { skipLocationChange: false, replaceUrl: true });
      return;
    }

    if (this._button.identifier == ReservedButton.RESET) {
      FormUtils.reset(this.form);
      this.context = {};
    }

    let action: Action = ButtonUtils.getAction(
                            this.sourceIdentifier, 
                            this.sourceIndex, 
                            this.widgetArrayIndex, 
                            this._button.identifier, 
                            this.parentHierarchy, 
                            event, 
                            this.originalData, 
                            this.context, 
                            actionDialog);

    if (this._button.identifier == ReservedButton.SEARCH || this._button.identifier == ReservedButton.RESET) {
      this.addSearchParamsInUrl(action.data);
    } else {
      this.onClick.emit(action);
    }
  }

  iconClick(event: any): void {
    let action: Action = ButtonUtils.getAction(
                            this.sourceIdentifier, 
                            this.sourceIndex, 
                            this.widgetArrayIndex, 
                            this._button.identifier, 
                            this.parentHierarchy, 
                            event, 
                            this.originalData, 
                            this.context, 
                            null);

    if (this._button['groupIdentifier'] == ReservedButton.CLEAR_FILTER_FIELD) {
      delete action.data[action.action];
      this.addSearchParamsInUrl(action.data);
    } else  {
      this.onIconClick.emit(action);
    }
  }
  
  public addSearchParamsInUrl(filterData: any) {
    const queryParams: Params = { filter: SecurityUtils.encrypt(filterData) };

    this.router.navigate(
      [],
      {
        relativeTo: this.route,
        queryParams: queryParams,
        queryParamsHandling: 'merge', // remove to replace all query params by provided
        skipLocationChange: false,
        replaceUrl: true
      });
  }

  setButtonEnable(): void {
    let isButtonDisabled: boolean = false;

    if (this.form) {
      isButtonDisabled = !this.form.valid;
    }
    if (this.form && this.form.disabled) {
      isButtonDisabled = false;
    }
    if (this._button.identifier == ReservedButton.SEARCH || this._button.identifier == ReservedButton.CANCEL || this._button.identifier == ReservedButton.RESET || this._button.identifier == ReservedButton.ADD_FIELD || this._button.identifier == ReservedButton.REMOVE_FIELD || this._button.alwaysEnable == true) {
      isButtonDisabled = false;
    }
    if (this.disabled) {
      isButtonDisabled = this.disabled;
    }
    this.isButtonDisabled = isButtonDisabled;
  }

  setOnlyIconDisplay(): void {
    this.isDisplayOnlyIcon = this._button && this._button.onlyIcon && !StringUtils.isEmpty(this._button.icon) ? true : false;
  }

  setIconDisplay(): void {
    this.hasIconDisplay = this._button && this._button.icon ? true : false;
  }

  setFlatButton(): void {
    this.isFlatButton = this._button && ButtonUtils.instanceOfButton(this._button) ? (<Button>this._button).type == ButtonType.FLAT && !this.isGroupButton && !this.isLink : false;
  }

  setGhostButton(): void {
    this.isGhostButton = this._button && ButtonUtils.instanceOfButton(this._button) ? (<Button>this._button).type == ButtonType.GHOST && !this.isGroupButton && !this.isLink : false;
  }

  setRaisedButton(): void {
    this.isRaisedButton = this._button && ButtonUtils.instanceOfButton(this._button) ? (<Button>this._button).type == ButtonType.RAISED && !this.isGroupButton && !this.isLink : false;
  }

  setStrokedButton(): void {
    this.isStrokedButton = this._button && ButtonUtils.instanceOfButton(this._button) ? (<Button>this._button).type == ButtonType.STROKED && !this.isGroupButton && !this.isLink : false;
  }

  setFabButton(): void {
    this.isFabButton = this._button && ButtonUtils.instanceOfButton(this._button) ? (<Button>this._button).type == ButtonType.FAB && !this.isGroupButton && !this.isLink : false;
  }

  setChip(): void {
    this.isChip = this._button && ButtonUtils.instanceOfChipButton(this._button) && !StringUtils.isEmpty((<ButtonGroup>this._button).groupIdentifier) ? true : false;
  }

  setLink(): void {
    this.isLink = this._button && !StringUtils.isEmpty(this.label) ? !CollectionUtils.isEmpty((<Button>this._button).routerLink) : false;
    this.routerLink = (<Button>this._button).routerLink;
  }

  setGroupButton(): void {
    this.isGroupButton = this._button && ButtonUtils.instanceOfButtonGroup(this._button) && !StringUtils.isEmpty((<ButtonGroup>this._button).groupIdentifier) ? true : false;
  }

  setMenuButton(): void {
    this.isMenuButton = this._button && ButtonUtils.instanceOfHoverButton(this._button) ? true : false;
  }

  ngOnDestroy() {
    if (this.formSubscription) {
      this.formSubscription.unsubscribe();
    }
  }
}
