import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ObjectUtils, StringUtils } from '../../../utility';
import { ConfirmationPopupButton, ConfirmationPopup, ObjectTree } from '../../model';
import { FormGroup } from '@angular/forms';
import { AbilityUtils } from '../../../utility/ability.utility';
import { Ability } from '@casl/ability';

export interface DialogData {
  form: FormGroup,
  sourceIdentifier: string,
  sourceIndex: number,
  widgetArrayIndex: number,
  context: any,
  originalData: any, 
  buttonRoute: Array<string>,
  parentHierarchy: ObjectTree,
  confirmationConfig: ConfirmationPopup;
}

@Component({
  selector: 'mx-button-delete-confirmation',
  templateUrl: './button-delete-confirmation.component.html',
  styleUrls: ['./button-delete-confirmation.component.scss']
})
export class ButtonDeleteConfirmationComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<ButtonDeleteConfirmationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private ability: Ability) { 
      AbilityUtils.setAbility(this.ability);
    } 

  title: string;
  message: string;

  ngOnInit(): void {
    this.title = this.resolve(this.data.confirmationConfig.title);
    this.message = this.resolve(this.data.confirmationConfig.message);
  }

  displayOnlyIcon(button: ConfirmationPopupButton): boolean {
    return button && !StringUtils.isEmpty(button.icon) && StringUtils.isEmpty(button.label) ? true : false;
  }

  displayIcon(button: ConfirmationPopupButton): boolean {
    return button && !StringUtils.isEmpty(button.icon) ? true : false;
  }

  resolve(text: string): string {
    return ObjectUtils.resolve(text, this.data.originalData);
  }

  click(event: any) {
    this.dialogRef.close(event);
  }
}
