import { DependentOnField } from "../field/model";
import { FormButton, FormButtonLayout, FormField } from "../form/model";
import { CollectionUtils } from "./collection.utility";

export class DependentUtils {
  static displayDependencyField(dependentOnFields: Array<DependentOnField>, supportingRecord: any, record: any): boolean {
    let displayDependencyField: boolean = true;
    let displaySupportingDependencyField: boolean = true;

    if (dependentOnFields && dependentOnFields.length > 0 && record) {
      for (let dependentField of dependentOnFields) {
        let dependencyValue: any = "";
        try {
          dependencyValue = eval("record." + dependentField.key);
        } catch (e) {

        }

        if (dependencyValue != undefined) {
          if (dependentField.condition instanceof Array) {
            if (dependencyValue instanceof Array) {
              let hasMatch: boolean = false;
              (<Array<any>>dependencyValue).forEach(value => {
                if ((<Array<string>>dependentField.condition).indexOf(value) > -1) {
                  hasMatch = true;
                }
              });
              if (hasMatch) {
                displayDependencyField = false;
              }
            } else {
              if ((<Array<string>>dependentField.condition).indexOf(dependencyValue) <= -1) {
                displayDependencyField = false;
              }
            }
          } else {
            if (dependencyValue instanceof Array) {
              if (dependencyValue.indexOf(dependentField.condition) <= -1) {
                displayDependencyField = false;
              }
            } else {
              if (dependencyValue != dependentField.condition) {
                displayDependencyField = false;
              }
            }
          }
        }
      }
    }

    if (dependentOnFields && dependentOnFields.length > 0 && supportingRecord) {
      for (let dependentField of dependentOnFields) {
        let dependencyValue: any = "";
        try {
          dependencyValue = eval("supportingRecord." + dependentField.key);
        } catch (e) {

        }

        if (dependencyValue != undefined) {
          if (dependentField.condition instanceof Array) {
            if (dependencyValue instanceof Array) {
              let hasMatch = false;
              (<Array<any>>dependencyValue).forEach(value => {
                if ((<Array<string>>dependentField.condition).indexOf(value) > -1) {
                  hasMatch = true;
                }
              });
              if (!hasMatch) {
                displaySupportingDependencyField = false;
              }
            } else {
              if ((<Array<string>>dependentField.condition).indexOf(dependencyValue) <= -1) {
                displaySupportingDependencyField = false;
              }
            }
          } else {
            if (dependencyValue instanceof Array) {
              if (dependencyValue.indexOf(dependentField.condition) <= -1) {
                displaySupportingDependencyField = false;
              }
            } else {
              if (dependencyValue != dependentField.condition) {
                displaySupportingDependencyField = false;
              }
            }
          }
        }
      }
    }

    return displayDependencyField && displaySupportingDependencyField;
  }

  static getDependencyTree(formFields: Array<FormField>): any {
    let dependency: any = {};

    for (let formField of formFields) {
      if (formField.field.dependentOnFields && formField.field.dependentOnFields.length > 0) {
        for (let dependentField of formField.field.dependentOnFields) {
          if (dependency[dependentField.key] == undefined || dependency[dependentField.key] == null) {
            dependency[dependentField.key] = new Array<string>();
          }
          dependency[dependentField.key].push(formField.field.key);
        }
      }
    }

    return dependency;
  }

  static getDependencyTreeForButton(buttonLayout: FormButton | FormButtonLayout): any {
    let dependency: any = {};

    if (buttonLayout && !CollectionUtils.isEmpty((<FormButton>buttonLayout).buttons)) {
      for (let button of (<FormButton>buttonLayout).buttons) {
        if (button.dependentOnFields && button.dependentOnFields.length > 0) {
          for (let dependentField of button.dependentOnFields) {
            if (dependency[dependentField.key] == undefined || dependency[dependentField.key] == null) {
              dependency[dependentField.key] = new Array<string>();
            }
            dependency[dependentField.key].push(button.identifier);
          }
        }
      }
    }

    if (buttonLayout && !CollectionUtils.isEmpty((<FormButtonLayout>buttonLayout).cells)) {
      for (let cell of (<FormButtonLayout>buttonLayout).cells) {
        if ((<FormButton>cell.buttons) && !CollectionUtils.isEmpty((<FormButton>cell.buttons).buttons)) {
          for (let button of (<FormButton>cell.buttons).buttons) {
            if (button.dependentOnFields && button.dependentOnFields.length > 0) {
              for (let dependentField of button.dependentOnFields) {
                if (dependency[dependentField.key] == undefined || dependency[dependentField.key] == null) {
                  dependency[dependentField.key] = new Array<string>();
                }
                dependency[dependentField.key].push(button.identifier);
              }
            }
          }
        }
      }
    }

    return dependency;
  }
}