import { Ability, RawRule } from '@casl/ability';

export class AbilityUtils {
    static setAbility(ability: Ability): void {
        let user: any = JSON.parse(localStorage.getItem("user"));

        if (user && user['permissions']) {
            let rules: Array<RawRule> = <Array<RawRule>>user['permissions'];
            ability.update(<any>rules);
        }
    }
}
