import { Component, OnInit, Input, ViewEncapsulation, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Button, ObjectTree, ButtonGroup, HoverButton, Badge, ButtonType } from '../../model';
import { ButtonGroupList, HoverButtonList } from '../../model/button-group.model';
import { FormDiaplyMode } from '../../../form/model';
import { ObjectUtils, ButtonUtils, StringUtils, CollectionUtils } from '../../../utility';
import { AbilityUtils } from '../../../utility/ability.utility';
import { Ability } from '@casl/ability';

//export { Button, ObjectTree, ButtonGroup, HoverButton, Badge, ButtonType } from '../../model';

@Component({
  selector: 'mx-button-group',
  templateUrl: './button-group.component.html',
  styleUrls: ['./button-group.component.scss', '../button/button.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ButtonGroupComponent implements OnInit {
  @Input() form: FormGroup;
  @Input() formDisplayMode: FormDiaplyMode;

  @Input() sourceIdentifier: string;
  @Input() sourceIndex: number;
  @Input() widgetArrayIndex: number;
  @Input() _buttons: Array<Button | ButtonGroup | HoverButton>;

  get buttons(): Array<Button | ButtonGroup | HoverButton> {
    return this._buttons;
  }
  @Input()
  set buttons(_buttons: Array<Button | ButtonGroup | HoverButton>) {
    if (!(_buttons instanceof Array)) {
      let btns = new Array<Button | ButtonGroup | HoverButton>();
      btns.push(_buttons);
      _buttons = btns;
    }
    this._buttons = _buttons;

    this.getButtonList();
    this.getButtonType();

    this.displayDesktopDD = this.hasMobileButton('showOnDesktop');
    this.displayTabletDD = this.hasMobileButton('showOnTablet');
    this.displayMobileDD = this.hasMobileButton('showOnMobile');

    if (this.displayDesktopDD || this.displayMobileDD || this.displayTabletDD)  {
      this.changeButtonTypeToGhost();
    }
  }

  @Input() context: any;
  @Input() originalData: any;
  @Input() buttonRoute: Array<string>;
  @Input() parentHierarchy: ObjectTree;
  @Input() disabled: boolean;
  @Output() onClick = new EventEmitter();
  @Output() onIconClick = new EventEmitter();

  buttonLists: Array<Array<Button> | ButtonGroupList | HoverButtonList> = new Array<Array<Button> | ButtonGroupList | HoverButtonList>();
  buttonListsType: Array<string> = new Array<string>();

  displayDesktopDD: boolean = false;
  displayTabletDD: boolean = false;
  displayMobileDD: boolean = false;
  hoverButtonLists: Array<Array<Button> | ButtonGroupList | HoverButtonList> = new Array<Array<Button> | ButtonGroupList | HoverButtonList>();

  constructor(private ability: Ability) {
    AbilityUtils.setAbility(this.ability);
  }

  ngOnInit(): void {
    // this.getButtonList();
    // this.getButtonType();
  }

  hasMobileButton(displayIn: string): boolean {
    let hasButtonsForDropdown: boolean = false;

    if (!CollectionUtils.isEmpty(this.buttonLists)) {
      this.buttonLists.forEach(buttons => {
        if (!CollectionUtils.isEmpty(buttons) && buttons instanceof Array) {
          (<Array<Button>>buttons).forEach(button => {
            if (button['showOnDesktop'] == null && button['showOnTablet'] == null && button['showOnMobile'] == null)  {
              button['showOnDesktop'] = true;
              button['showOnTablet'] = true;
              button['showOnMobile'] = true; 
            }

            if (button[displayIn]) {
              hasButtonsForDropdown = true;
            }
          })
        }
      });
    }

    return hasButtonsForDropdown;
  }
 
  changeButtonTypeToGhost() {
    this.hoverButtonLists = JSON.parse(JSON.stringify(this.buttonLists));

    if (!CollectionUtils.isEmpty(this.hoverButtonLists)) {
      this.hoverButtonLists.forEach(buttons => {
        if (!CollectionUtils.isEmpty(buttons) && buttons instanceof Array) {
          (<Array<Button>>buttons).forEach(button => {
            button.type = ButtonType.GHOST;
          })
        }
      });
    }
  }

  getButtonList() {
    this.buttonLists = new Array<Array<Button> | ButtonGroupList | HoverButtonList>();
    let buttonArray: Array<Array<Button>> = new Array<Array<Button>>();
    let buttonArrayIndex: number = 0;

    if (this._buttons) {
      for (let index = 0; index < this._buttons.length; index++) {
        if (ButtonUtils.instanceOfButton(this._buttons[index])) {
          if (ObjectUtils.isEmpty(buttonArray[buttonArrayIndex])) {
            buttonArray[buttonArrayIndex] = new Array<Button>();

            this.buttonLists.push(buttonArray[buttonArrayIndex]);
          }

          buttonArray[buttonArrayIndex].push(<Button>this._buttons[index]);
        }
        if (ButtonUtils.instanceOfButtonGroup(this._buttons[index]) || ButtonUtils.instanceOfChipButton(this._buttons[index])) {
          let bgAdded = this.getGroupButton((<ButtonGroup>this._buttons[index]).groupIdentifier, this.buttonLists)

          if (bgAdded) {
            buttonArrayIndex++;
          }
        }
      }

      let hoverButton: HoverButtonList = this.getHoverButtons();
      if (hoverButton && hoverButton.hoverButtons && hoverButton.hoverButtons.length > 0) {
        this.buttonLists.push(hoverButton);
        buttonArrayIndex++;
      }
    }
  }

  isGroupAdded(groupIdentifier: string, buttonLists: Array<Array<Button> | ButtonGroupList | HoverButtonList>): boolean {
    let groupAdded: boolean = false;

    if (buttonLists) {
      groupAdded = buttonLists.filter(buttonList => buttonList && (<ButtonGroupList>buttonList).groupIdentifier == groupIdentifier).length > 0;
    }

    return groupAdded;
  }

  getGroupButton(groupIdentifier: string, buttonLists: Array<Array<Button> | ButtonGroupList | HoverButtonList>): boolean {
    let bgAdded: boolean = false;
    let gButtons: Array<ButtonGroup> = new Array<ButtonGroup>();

    if (this._buttons && this.isGroupAdded(groupIdentifier, buttonLists) == false) {
      gButtons = this._buttons.filter(button => (<ButtonGroup>button).groupIdentifier == groupIdentifier).map(button => {
        button.width = '100';
        return <ButtonGroup>button;
      });

      // To check if any button is fullwidth
      let width = gButtons.filter(gButton => gButton.fullWidth == true).length > 0 ? '100' : 'auto';
      let buttonWidth: any = 'auto';
      if (width != 'auto') {
        buttonWidth = (+width / gButtons.length);
      }

      buttonLists.push({
        groupIdentifier: groupIdentifier,
        width: width,
        buttonWidth: buttonWidth,
        groupButtons: gButtons
      });
      bgAdded = true;
    }

    return bgAdded;
  }

  getHoverButtons(): HoverButtonList {
    let hoverButtons: Array<HoverButton> = new Array<HoverButton>();
    let groupIdentifier: string;
    let groupLabel: string;
    let groupIcon: string;
    let badge: Badge;
    let width: string = 'auto';

    if (this._buttons) {
      this._buttons.filter(button => ButtonUtils.instanceOfHoverButton(button)).forEach(button => {
        if (StringUtils.isEmpty(groupIdentifier)) {
          groupIdentifier = (<HoverButton>button).groupIdentifier;
        }
        if (StringUtils.isEmpty(groupLabel)) {
          groupLabel = (<HoverButton>button).groupLabel;
        }
        if (StringUtils.isEmpty(groupIcon)) {
          groupIcon = (<HoverButton>button).groupIcon;
        }
        if (CollectionUtils.isEmpty(badge)) {
          badge = (<HoverButton>button).badge;
        }
        if ((<HoverButton>button).fullWidth) {
          width = '100';
          button.width = '100';
        }

        hoverButtons.push(<HoverButton>button);
      });
    }

    return { groupIdentifier: groupIdentifier, groupLabel: groupLabel, groupIcon: groupIcon, badge: badge, width: width, hoverButtons: hoverButtons };
  }

  getButtonType(): void {
    this.buttonListsType = new Array<string>();

    if (this.buttonLists && this.buttonLists.length > 0) {
      for (let buttonList of this.buttonLists) {
        let buttonType: string = "";
        if (this.isButtonGroup(buttonList)) {
          buttonType = 'ButtonGroup';
        } else if (this.isChipGroup(buttonList)) {
          buttonType = 'ChipGroup';
        } else if (this.isButtonHover(buttonList)) {
          buttonType = 'ButtonHover';
        } else if (this.isButtonArray(buttonList)) {
          buttonType = 'ButtonArray';
        }

        this.buttonListsType.push(buttonType);
      }
    }
  }

  isButtonGroup(buttonList: Array<Button> | ButtonGroupList | HoverButtonList): boolean {
    let isButtonGroup: boolean = false;

    if (buttonList && (<ButtonGroupList>buttonList).groupButtons && (<ButtonGroupList>buttonList).groupButtons.length > 0 && !StringUtils.isEmpty((<ButtonGroupList>buttonList).groupIdentifier)) {
      isButtonGroup = (<ButtonGroupList>buttonList).groupButtons.filter(button => ButtonUtils.instanceOfButtonGroup(button)).length > 0;
    }

    return isButtonGroup;
  }

  isChipGroup(buttonList: Array<Button> | ButtonGroupList | HoverButtonList): boolean {
    let isChipGroup: boolean = false;

    if (buttonList && (<ButtonGroupList>buttonList).groupButtons && (<ButtonGroupList>buttonList).groupButtons.length > 0 && !StringUtils.isEmpty((<ButtonGroupList>buttonList).groupIdentifier)) {
      isChipGroup = (<ButtonGroupList>buttonList).groupButtons.filter(button => ButtonUtils.instanceOfChipButton(button)).length > 0;

      if (isChipGroup) {
        (<ButtonGroupList>buttonList).groupButtons.forEach(button => ButtonUtils.instanceOfChipButton(button));
      }
    }

    return isChipGroup;
  }

  isButtonHover(buttonList: Array<Button> | ButtonGroupList | HoverButtonList): boolean {
    return buttonList && (<HoverButtonList>buttonList).hoverButtons && (<HoverButtonList>buttonList).hoverButtons.length > 0
  }

  isButtonArray(buttonList: Array<Button> | ButtonGroupList | HoverButtonList): boolean {
    return buttonList instanceof Array;
  }

  click(event: any) {
    this.onClick.emit(event);
  }

  iconClick(event: any) {
    this.onIconClick.emit(event);
  }
}
