import { ENTER, SPACE } from '@angular/cdk/keycodes';
import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormControl, NgControl } from '@angular/forms';
import { MatAutocomplete, MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';
import { MatChipInputEvent } from '@angular/material/chips';
import { ChipField, Chip, ChipItemChangedEvent, ChipOrientation, RemoveFunc } from '../../model/chips.model';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ErrorStateMatcher } from '@angular/material/core';
import { DropdownOption } from '../../model';
import { FieldComponent } from '../field/field.component';
import { Ability } from '@casl/ability';
import { Router } from '@angular/router';
import { CollectionUtils, FieldUtils } from '../../../utility';
import { MatFormFieldControl } from '@angular/material/form-field';
import { Observable } from 'rxjs';

@Component({
    selector: 'mx-chips',
    templateUrl: './chips.component.html',
    styleUrls: ['./chips.component.scss'],
    // providers: [
    //     { provide: MatFormFieldControl, useExisting: ChipsComponent }
    // ]
})
export class ChipsComponent extends FieldComponent implements OnInit {
    @Input() field: ChipField = null;
    @Input() disabled: boolean;
    @Input() formControl: FormControl;

    @Input() errorMatcher: ErrorStateMatcher;

    @ViewChild('input') input: ElementRef<HTMLInputElement>;
    @ViewChild('auto') matAutocomplete: MatAutocomplete;

    @Output() onListChange: EventEmitter<ChipItemChangedEvent> = new EventEmitter();;

    placeholder: string = '';
    separatorKeysCodes: number[] = [ENTER];//SPACE
    enableDragAndDrop: boolean;
    orientation: ChipOrientation;
    chips: Chip[] = [];
    options: Array<DropdownOption>;
    addOnBlur: boolean = true;
    removable: boolean;

    constructor(public router: Router, public ability: Ability) {
        super(router, ability);
    }
    stateChanges: Observable<void>;
    id: string;
    ngControl: NgControl;
    focused: boolean;
    empty: boolean;
    shouldLabelFloat: boolean;
    errorState: boolean;
    controlType?: string;
    autofilled?: boolean;
    userAriaDescribedBy?: string;
    setDescribedByIds(ids: string[]): void {
        throw new Error('Method not implemented.');
    }
    onContainerClick(event: MouseEvent): void {
        throw new Error('Method not implemented.');
    }

    ngOnInit() {
        this.placeholder = this.field.placeholder || '';
        this.orientation = this.field.orientation || ChipOrientation.HORIZONTAL;
        this.options = this.field.options || [];

        this.setChips();
    }

    setChips() {
        let values: Array<string> = this.formControl.value;
        let isReadOnly: boolean = FieldUtils.isFieldDisabled(this.field, this.displayMode, values);

        if (!CollectionUtils.isEmpty(values)) {
            values.forEach(value => {
                if (!CollectionUtils.isEmpty(this.field.options)) {
                    let optionMatch: DropdownOption = null;
                    this.field.options.forEach(option => {
                        if (option.key == value) {
                            optionMatch = option;
                        }
                    });

                    if (optionMatch != null) {
                        this.chips.push({
                            key: optionMatch.key,
                            value: optionMatch.value,
                            disabled: isReadOnly,
                            removable: true
                        });
                    } else {
                        this.chips.push({
                            key: value,
                            value: value,
                            disabled: isReadOnly,
                            removable: true
                        });
                    }
                } else {
                    this.chips.push({
                        key: value,
                        value: value,
                        disabled: isReadOnly,
                        removable: true
                    });
                }
            });
        } else {
            this.chips = [];
        }
    }

    addItem = ($event: MatChipInputEvent): void => {
        const input = $event.input;
        const value = $event.value;

        let keyExists = this.keyExists(value.trim());

        // Add our item
        if ((value || '').trim() && !keyExists) {
            this.chips.push({
                key: value.trim(),
                value: value.trim(),
                disabled: false,
                removable: this.removable || true
            });
        }

        // Reset the input value
        if (input) {
            input.value = '';
        }
        this.triggerChangedEvent($event as any);
    }

    keyExists(value: string): boolean {
        let keyExists = false;
        this.chips.forEach(chip => {
            if (chip.key == value) {
                keyExists = true;
            }
        });

        return keyExists;
    }

    removeItem = ($event: Event, item: Chip, index: number) => {
        this.chips.splice(index, 1);
        // if (this.isFunctionDefined(this.field.onRemoveItem)) {
        //     this.field.onRemoveItem(item.value).subscribe(result => {
        //         if (result) {
        //             this.chips.splice(index, 1);
        //             this.triggerChangedEvent($event);
        //         }
        //     });
        // }
    }

    drop = ($event: CdkDragDrop<Chip[]>) => {
        moveItemInArray(this.chips, $event.previousIndex, $event.currentIndex);
        this.triggerChangedEvent($event as any);
    }

    triggerChangedEvent = ($event: Event | MatAutocompleteSelectedEvent) => {
        let values: Array<string> = this.chips.map(x => x.key);
        this.formControl.setValue(values);
        this.onListChange.emit(<ChipItemChangedEvent>{
            values: values,
            event: $event
        });
    }

    onSelected = (event: MatAutocompleteSelectedEvent): void => {
        let keyExists = this.keyExists(event.option.value['key']);

        if (!keyExists) {
            this.chips.push({
                key: event.option.value['key'],
                value: event.option.value['value'],
                removable: true,
                disabled: false
            });
            this.input.nativeElement.value = '';
            this.triggerChangedEvent(event);
        }
    }

    isFunctionDefined = (func: RemoveFunc) => {
        return typeof func == 'function';
    }

    errors(): Array<{ error: string, message: string }> {
        let errors: Array<{ error: string, message: string }> = [];
        if (this.formControl != undefined) {
            if (this.formControl && this.formControl.errors) {

                Object.keys(this.formControl.errors).forEach(keyError => {
                    if (this.field.validations) {
                        for (let validation of this.field.validations) {
                            if (keyError === validation.message.key) {
                                errors[errors.length] = { error: keyError, message: validation.message.message };
                            }
                        }
                    }
                });
            }
        }
        return errors;
    }
}
