import { Form, FormButton, FormButtonLayout, FormCellControl, FormCellControlType, FormCustomLayout, FormField } from "../form/model";
import { CollectionUtils } from "./collection.utility";
import { StringUtils } from "./string.utility";


export class FormCustomUtils {
    static setCustomLayout(formConfig: Form, layout: FormCustomLayout) { 
        if (layout && !CollectionUtils.isEmpty(layout.cells)) {
            layout.cells.forEach(cell => {
                if (!CollectionUtils.isEmpty(cell.controls)) {
                    cell.controls.forEach(control => {
                        if (CollectionUtils.isEmpty(control.control))   {
                            let resolvedControl: any = {};
                            resolvedControl = this.getControl(formConfig, control);

                            control.control = resolvedControl;

                            // if (control.type == CellControllType.BUTTON && control.control['type'] == ButtonType.CHIP) {
                            //     control.control['groupIdentifier'] = control.control.identifier + cell.rows + cell.cols;
                            // }
                        }
                    });
                }
            })
        }
        console.log(formConfig.layout)
    }

    static getControl(formConfig: Form, cellControl: FormCellControl): any {
        let control: any = {};

        if (cellControl.control) {
            control = cellControl.control;
        } else if (!CollectionUtils.isEmpty(formConfig) && !CollectionUtils.isEmpty(formConfig.formFields) && cellControl.type == FormCellControlType.FIELD) {
            control = this.getFieldControl(formConfig.formFields, cellControl.key);
        } else if (!CollectionUtils.isEmpty(formConfig) && !CollectionUtils.isEmpty(formConfig.action) && cellControl.type == FormCellControlType.BUTTON) {
            control = this.getButtonControl(formConfig.action, cellControl.key);
        }

        return control;
    }

    static getFieldControl(formFields: Array<FormField>, controlKey: string): any {
        let control: any = {};

        formFields.forEach(formField => {
            if (!CollectionUtils.isEmpty(formField.field) && formField.field.key == controlKey) {
                control = formField;
            }
        })

        return control;
    }

    static getButtonControl(action: FormButton | FormButtonLayout, controlKey: string): any {
        let control: any;

        if (StringUtils.isEmpty(action['rowHeight']))   {
            (<FormButton>action).buttons.forEach(button => {
                if (button.identifier == controlKey) {
                    control = button;
                }
            });
        }   else    {
            (<FormButtonLayout>action).cells.forEach(cell => {
                cell.buttons.buttons.forEach(button => {
                    if (button.identifier == controlKey) {
                        control = button;
                    }
                });
            })
        }

        return control;
    }
}