import { FormDiaplyMode, Form, FormField, FormButton } from '../form/model';
import { Field, FieldType, AutocompleteField, RadioField, CheckboxField, DropdownField, KeyMap, DependentOnField, CalendarField, ParagraphField } from '../field/model';
import { FormGroup, FormControl, ValidatorFn, FormArray } from '@angular/forms';
//import { isArray } from 'util';
import { ObjectUtils } from './object.utility';
import { CollectionUtils } from './collection.utility';
import { StringUtils } from './string.utility';
import { FieldUtils } from './field.utility';
import { DependentUtils } from './dependent.utility';

export class FormUtils {
  static isFormDisable(displayMode: FormDiaplyMode): boolean {
    if (displayMode == FormDiaplyMode.BLOCKED || displayMode == FormDiaplyMode.VIEW) {
      return true;
    } else { 
      return false;
    }
  }

  static disableField(form: FormGroup, field: Field): void {
    if (form && form.get(field.key)) {
      form.get(field.key).disable();
    }
  }

  static disableFieldControl(fieldControl: FormControl): void {
    if (fieldControl) {
      fieldControl.disable();
    }
  }

  static checkUniqueIdentifier(forms: Array<Form>) {
    //  TODO: 
  }

  static getRawValue(form: FormGroup) {
    let formValue: any = {};

    if (form) {
      formValue = form.getRawValue();
    }

    return formValue;
  }

  static initFormGroup(formFields: Array<FormField>, supportingRecord: any, record: any, displayMode: FormDiaplyMode): FormGroup {
    let fieldControls = {};

    let isFormDisabled = this.isFormDisable(displayMode);

    for (let formField of formFields) {
      // if (!CollectionUtils.isEmpty(record)) {
      FormUtils.initFieldGroup(fieldControls, formField, supportingRecord, record, displayMode);
      // }
    }

    return new FormGroup(fieldControls);
  }

  static getFieldValidation(field: Field): Array<ValidatorFn> {
    let validations: Array<ValidatorFn> = new Array<ValidatorFn>();
    if (field.validations && field.validations.length > 0) {
      for (let val of field.validations) {
        validations.push(val.type);
      }
    }

    return validations;
  }

  static initFieldGroup(fieldControls: any, formField: FormField, supportingRecord: any, record: any, displayMode: FormDiaplyMode): any {
    //        field.hide = false;
    if (!DependentUtils.displayDependencyField(formField.field.dependentOnFields, supportingRecord, record)) {
      if (record == null) {
        record = {};
      }
      if (formField.addMore) {
        record[formField.field.key] = [];
      } else {
        record[formField.field.key] = null;
      }
      //          field.hide = true;
      return;
    }

    let validations: Array<ValidatorFn> = this.getFieldValidation(formField.field);

    if (formField.field.type == FieldType.CHECKBOX) {
      let fieldValue = FormUtils.getValueViaEval(record, formField.field.key);
      if (formField.addMore) {
        let options: Array<any> = record && fieldValue ? fieldValue : new Array<any>();

        fieldControls[formField.field.key] = new FormArray([]);
        for (let vCnt = 0; vCnt < options.length; vCnt++) {
          let opts = this.getCheckboxOption(displayMode, formField.field, options[vCnt]);
          fieldControls[formField.field.key].push(this.setFormGroup(displayMode, formField.field, opts, validations));
        }
      } else {
        let option = record && fieldValue ? fieldValue : null;

        let opts = this.getCheckboxOption(displayMode, formField.field, option);
        fieldControls[formField.field.key] = this.setFormGroup(displayMode, formField.field, opts, validations);
      }
    } else if (formField.field.type == FieldType.CALENDAR && (<CalendarField>formField.field).dateRange) {
      let controls = {};
      if (validations && validations.length > 0) {
        controls['startDate'] = new FormControl('',validations);
        controls['endDate'] = new FormControl('', validations);
      } else {
        controls['startDate'] = new FormControl('');
        controls['endDate'] = new FormControl('');
      }
      fieldControls[formField.field.key] = this.setFormGroup(displayMode, formField.field, controls, validations);
    } else if (formField.field.type == FieldType.PARAGRAPH && !CollectionUtils.isEmpty((<ParagraphField>formField.field).fieldContexts)) {
      let fieldValue = FormUtils.getValueViaEval(record, formField.field.key);
      let controls = this.getParagraphFields(displayMode, formField.field, fieldValue);

      fieldControls[formField.field.key] = this.setFormGroup(displayMode, formField.field, controls, validations);
    } else {
      if (formField.addMore) {
        let values: Array<any> = this.getFormFieldValue(record, formField);

        fieldControls[formField.field.key] = new FormArray([]);
        if (CollectionUtils.isEmpty(values)) {
          fieldControls[formField.field.key].push(this.setFormControl(displayMode, formField.field, "", validations));
        } else {
          for (let vCnt = 0; vCnt < values.length; vCnt++) {
            fieldControls[formField.field.key].push(this.setFormControl(displayMode, formField.field, values[vCnt], validations));
          }
        }
      } else {
        let value: any = this.getFormFieldValue(record, formField)

        fieldControls[formField.field.key] = this.setFormControl(displayMode, formField.field, value, validations);
      }
    }

    return fieldControls;
  }

  static dateRangeValidation(g: FormGroup) : any{
    return g.get('startDate').value || g.get('endDate').value
    ? null : {'mismatch': true};
  }
  static getCheckboxOption(displayMode: FormDiaplyMode, field: Field, options: Array<string>): any {
    let opts = {};

    if (field['options'] && field['options'].length > 0) {
      for (let opt of field['options']) {
        if (ObjectUtils.isEmpty(options)) {
          if (opt['selected'])  {
            opt.checked = true;
          } else  {
            opt.checked = false;
          }
        } else {
          let optionSelected: boolean = options.indexOf(opt.key) > -1;
          if (options && optionSelected) {
            opt.checked = true;
          } else {
            opt.checked = false;
          }
        }
        opts[opt.key] = new FormControl(opt.checked);

        if (FieldUtils.isFieldDisabled(field, displayMode, "")) {
          opts[opt.key].disable();
        }

      }
    }

    return opts;
  }

  static getParagraphFields(displayMode: FormDiaplyMode, field: Field, fieldValues: Map<string, any>): any {
    let fieldContexts = {};

    if (CollectionUtils.isEmpty(fieldValues)) {
      fieldValues = new Map<string, any>();
    }

    if ((<ParagraphField> field).fieldContexts && Object.keys((<ParagraphField> field).fieldContexts).length > 0) {
      for (let key of Object.keys((<ParagraphField> field).fieldContexts)) {
        let fieldContextKey = (<ParagraphField> field).fieldContexts[key];

        let validations: Array<ValidatorFn> = this.getFieldValidation(fieldContextKey);
        fieldContexts[fieldContextKey.key] = new FormControl(fieldValues[fieldContextKey.key], validations);

        if (FieldUtils.isFieldDisabled(field, displayMode, "")) {
          fieldContexts[fieldContextKey.key].disable();
        }
      }
    }

    return fieldContexts;
  }

  static setFormGroup(displayMode: FormDiaplyMode, field: Field, value: any, validations: Array<ValidatorFn>): FormGroup {
    let formGroup: FormGroup;

    if (field.validations && field.validations.length > 0) {
      formGroup = new FormGroup(value, validations);
    } else {
      formGroup = new FormGroup(value);
    }

    if ((FieldUtils.isFieldDisabled(field, displayMode, value) || field.isReadOnly || field.type == FieldType.HIDDEN) && !StringUtils.isEmpty(value)) {
      formGroup.disable();
    }

    return formGroup;
  }

  static setFormControl(displayMode: FormDiaplyMode, field: Field, value: any, validations: Array<ValidatorFn>): FormControl {
    let formControl: FormControl;

    if (field.validations && field.validations.length > 0) {
      formControl = new FormControl(value, validations);
    } else {
      formControl = new FormControl(value);
    }

    if (FieldUtils.isFieldDisabled(field, displayMode, value)) {
      formControl.disable();
    } else {
      formControl.enable();
    }

    return formControl;
  }

  static getFormFieldValue(record: any, formField: FormField): any {
    let value: any;
    let fieldValue = FormUtils.getValueViaEval(record, formField.field.key);

    if (formField.addMore) {
      let elements: Array<any> = new Array<any>();
      if (!ObjectUtils.isEmpty(record) && !CollectionUtils.isEmpty(fieldValue) && Array.isArray(fieldValue)) {
        elements = fieldValue;
      } else {
        elements.push("");
      }

      for (let cnt = 0; cnt < elements.length; cnt++) {
        elements[cnt] = this.getFieldValue(elements[cnt], formField.field);
      }
      value = elements;
    } else {
      let recordValue = "";
      if (record && fieldValue) {
        recordValue = fieldValue;
      }
      value = this.getFieldValue(recordValue, formField.field);
    }
    return value
  }

  static getFieldValue(recordValue: any, field: Field): any {
    let value: any = null;

    if (field.value) {
      value = field.value;
    }
    if (!StringUtils.isEmpty(recordValue)) {
      value = recordValue;
    }
    if (value == undefined || JSON.stringify(value) === '{}') {
      value = "";
    }

    return value;
  }

  static reset(form: FormGroup, formConfig?: Form): void {
    form.reset();

    if (formConfig != null) {
      for (let field of formConfig.formFields) {
        if ((<Field>field.field).type == FieldType.AUTOCOMPLETE) {
          field['default'] = null;
        }
      }
    }
  }

  static getValueViaEval(record: any, key: string): any {
    let value = "";
    try {
      if (StringUtils.isEmpty(value)) {
        value = record[key];
      }
      value = eval("record." + key);
    } catch (e) { }

    return value;
  }

  static setOptionsUsingKey(form: Form, fieldKey: string, masterDataKey: string) {
    if (!CollectionUtils.isEmpty(form) && !CollectionUtils.isEmpty(form.formFields)) {
      form.formFields.forEach(formField => {
        if (!CollectionUtils.isEmpty(formField) && !CollectionUtils.isEmpty(formField.field)) {
          if (formField.field.key == fieldKey && (formField.field.type == FieldType.AUTOCOMPLETE || formField.field.type == FieldType.CHECKBOX || formField.field.type == FieldType.RADIO || formField.field.type == FieldType.DROPDOWN)) {
            FieldUtils.setOptionsUsingKey(<AutocompleteField | RadioField | CheckboxField | DropdownField>formField.field, masterDataKey);
          }
        }
      })
    }
  }

  static setOptionsUsingValues(form: Form, fieldKey: string, keyMap: KeyMap, relaodAll: boolean, record: any) {
    if (!CollectionUtils.isEmpty(form) && !CollectionUtils.isEmpty(form.formFields)) {
      form.formFields.forEach(formField => {
        if (!CollectionUtils.isEmpty(formField) && !CollectionUtils.isEmpty(formField.field)) {
          let isMatchingDependendKeyMap: boolean = false;
          if (!StringUtils.isEmpty((<AutocompleteField | RadioField | CheckboxField | DropdownField>formField.field).optionDependsOn)) {
            //  get optionDependsOn field value
            let optionDependsOnValue = FormUtils.getValueViaEval(record, (<AutocompleteField | RadioField | CheckboxField | DropdownField>formField.field).optionDependsOn);

            if (keyMap.optionDependsOnValue == optionDependsOnValue) {
              isMatchingDependendKeyMap = true;
            }
          } else {
            isMatchingDependendKeyMap = true;
          }

          if (formField.field.key == fieldKey && (formField.field.type == FieldType.AUTOCOMPLETE || formField.field.type == FieldType.CHECKBOX || formField.field.type == FieldType.RADIO || formField.field.type == FieldType.DROPDOWN)) {
            if (isMatchingDependendKeyMap) {//} || relaodAll) {
              FieldUtils.setOptionsUsingValues(<AutocompleteField | RadioField | CheckboxField | DropdownField>formField.field, keyMap);
            }
          }
        }
      })
    }
  }

  static changeButtonLabelIcon(form: Form, buttonIdentifier: string, label?: string, icon?: string) {
    if (!CollectionUtils.isEmpty(form) && !CollectionUtils.isEmpty(<FormButton>form.action) && !CollectionUtils.isEmpty((<FormButton>form.action).buttons)) {
      (<FormButton>form.action).buttons.forEach(button => {

        if (!CollectionUtils.isEmpty(button) && button.identifier == buttonIdentifier) {
          if (!StringUtils.isEmpty(label)) {
            button.label = label;
          }
          if (!StringUtils.isEmpty(icon)) {
            button.icon = icon
          }
        }
      })
    }
  }
}