import { Component, OnInit, ViewEncapsulation } from '@angular/core';

import { BreakpointObserver } from '@angular/cdk/layout';

import { ListComponent } from '../list.component';
import { ListType, Pagination } from '../../model';
import { Params } from '@angular/router';
import { Action } from '../../../button/model';
import { Ability } from '@casl/ability';

@Component({
  selector: 'mx-dynamic-list',
  templateUrl: '../static-list/static-list.component.html',
  styleUrls: ['../static-list/static-list.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DynamicListComponent extends ListComponent implements OnInit {

  constructor(
    public ability: Ability,
    public breakpointObserver: BreakpointObserver
  ) {
    super(ability, breakpointObserver);
    window.onscroll = ()=> {this.sticky()};
  }
 
  ngOnInit() {
    this.init();
  }

  isList(): boolean {
    return this._listConfig.listType == ListType.DYNAMIC;
  }

  onSort(event) {
    const queryParams: Params = { sorton: event['active'], sortdir: event['direction'] };

    let action: Action = {
      sourceIdentifier: this.sourceIdentifier ? this.sourceIdentifier : this._listConfig.identifier,
      sourceIndex: this.sourceIndex ? this.sourceIndex : 0,
      widgetArrayIndex: this.widgetArrayIndex,
      action: this._listConfig.identifier,
      actionData: queryParams,
      data: this.record,
      originalData: this.record,
      parentHierarchy: this.parent,
      event: event
    };

    console.log(action);
    this.onSortChange.emit(action);
  }

  onPage(pageInfo: Pagination) {
    console.log(pageInfo);
    let action: Action = {
      sourceIdentifier: this.sourceIdentifier ? this.sourceIdentifier : this._listConfig.identifier,
      sourceIndex: this.sourceIndex ? this.sourceIndex : 0,
      widgetArrayIndex: this.widgetArrayIndex,
      action: this._listConfig.identifier,
      actionData: pageInfo,
      data: this.record,
      originalData: this.record,
      parentHierarchy: this.parent,
      event: pageInfo
    };

    console.log(action);
    this.onPageChange.emit(action);
  }
}
