import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import {animate, state, style, transition, trigger} from '@angular/animations';

import { BreakpointObserver } from '@angular/cdk/layout';

import { ListComponent } from '../list.component';
import { ListType, Pagination } from '../../model';
import { Params } from '@angular/router';
import { Action } from '../../../button/model';
import { Ability } from '@casl/ability';

@Component({
  selector: 'mx-static-list', 
  templateUrl: './static-list.component.html',
  styleUrls: ['./static-list.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ], 
  encapsulation: ViewEncapsulation.None
})
export class StaticListComponent extends ListComponent implements OnInit {
  constructor(public ability: Ability, public breakpointObserver: BreakpointObserver) {
    super(ability, breakpointObserver);
    window.onscroll = ()=> {this.sticky()};
  }

  ngOnInit() {
    this.init();
  }

  isList(): boolean {
    return this.listConfig.listType == ListType.STATIC;
  }

  onSort(event) {
    const queryParams: Params = { sorton: event['active'], sortdir: event['direction'] };

    this.expandedRow = null;
    
    let action: Action = {
      sourceIdentifier: this.sourceIdentifier ? this.sourceIdentifier : this._listConfig.identifier,
      sourceIndex: this.sourceIndex ? this.sourceIndex : 0,
      widgetArrayIndex: this.widgetArrayIndex,
      action: this._listConfig.identifier,
      actionData: queryParams,
      data: this.record,
      originalData: this.record,
      parentHierarchy: this.parent,
      event: event
    }

    console.log(action);
    this.onSortChange.emit(action);
  }

  onPage(pageInfo: Pagination) {
    console.log("scrolled:-",pageInfo)
    let action: Action = {
      sourceIdentifier: this.sourceIdentifier ? this.sourceIdentifier : this._listConfig.identifier,
      sourceIndex: this.sourceIndex ? this.sourceIndex : 0,
      widgetArrayIndex: this.widgetArrayIndex,
      action: this._listConfig.identifier,
      actionData: pageInfo,
      data: this.record,
      originalData: this.record,
      parentHierarchy: this.parent,
      event: pageInfo
    };

    console.log(action);
    this.onPageChange.emit(action);
  }

  ngOnDestroy() {
  }
}
