import { Component, OnInit, Input, Output, EventEmitter, ViewChild, Inject, OnDestroy } from '@angular/core';
import { Record, ListType, ListHeader } from '../../../list/model';
import { Button, HoverButton, ButtonGroup, ButtonColor, ButtonType, ButtonSize, Badge, IconPosition, Action, ReservedButton } from '../../../button/model';
import { KeyMap, FieldChange } from '../../../field/model';
import { Subscription } from 'rxjs';
import { Ability, RawRule } from '@casl/ability';
import { CrudHeader, CrudList, CrudSearch, SearchDisplayType, ActionPage, CrudListData, CrudListTabData, CrudListDisplayType, CrudStyle } from '../../model';
import { MatSidenav } from '@angular/material/sidenav';
import { MatBottomSheetRef, MatBottomSheet } from '@angular/material/bottom-sheet';
import { MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import { AbilityUtils, StringUtils, CollectionUtils } from '../../../utility';

@Component({
  selector: 'mx-crud-list',
  templateUrl: './crud-list.component.html',
  styleUrls: ['./crud-list.component.scss']
})
export class CrudListComponent implements OnInit, OnDestroy {
  @Input() identifier: string;
  @Input() header: CrudHeader;
  @Input() actions: Array<Button | HoverButton | ButtonGroup>;
  @Input() actionPages: Array<ActionPage>;

  @Input() quickLinks: Array<Button | HoverButton | ButtonGroup>;
  @Input() searchConfig: CrudSearch;
  @Input() listConfig: CrudList;

  @Input() formReset: boolean;

  @Input() originalData: any;
  @Input() _configData: CrudListData;
  get configData(): CrudListData {
    return this._configData;
  }
  @Input()
  set configData(_configData: CrudListData) {
    this._configData = _configData;
    this.setConfigAsPerTab();
  }

  @Input() keyMap: Array<KeyMap>;

  @Input() listReset: boolean;

  _expanded: boolean = false;
  get expanded(): boolean {
    return this._expanded;
  }
  @Input()
  set expanded(_expanded: boolean) {
    this._expanded = _expanded;
  }
  @Input()
  expandRowIndex: number;

  @Output() onFormChange = new EventEmitter();
  @Output() onFieldChange = new EventEmitter();
  @Output() onButtonClick = new EventEmitter();
  @Output() onButtonIconClick = new EventEmitter();

  @Output() onSortClick = new EventEmitter();
  @Output() onPageClick = new EventEmitter();
  @Output() onTabClick = new EventEmitter();

  title: string;
  showSearchForm: boolean = false;
  searchButton: Button;
  searchModalButton: Button;
  listHeaders: Array<ListHeader> = new Array<ListHeader>();

  badges: Array<Badge>;
  pageBackRoute: Array<string>;
  records: Array<Record>;
  searchData: any;
  searchOnFieldsCount: number = 0;
  searchOnFieldsButtons: Array<Button> = new Array<Button>();

  buttonSubscriber: Subscription;
  fieldSubscriber: Subscription;
  formSubscriber: Subscription;
  dialogRefSubscriber: Subscription;

  constructor(private _bottomSheet: MatBottomSheet, private ability: Ability) {
    AbilityUtils.setAbility(this.ability);
  }

  ngOnInit() {
    this.searchButton = this.setSearchButton('searchButton', 'Search', 'search');
    this.searchModalButton = this.setSearchButton('searchCloseButton', 'Close', 'close');
    this.displaySearchForm();

    this.setListHeaders();
    this.setCrudHeader();

    if (this.searchConfig && this.searchConfig.displayType != SearchDisplayType.ABOVE_LIST) {
      this.searchConfig.form.displayInColumns = 1;
    }
    this.setConfigAsPerTab(); 
  }

  setConfigAsPerTab(tabIdentifier?: string): void {
    if (this._configData) {
      this.badges = this._configData.badges;
      this.pageBackRoute = this._configData.pageBackRoute;

      this.records = this._configData.records;

      this.searchData = this._configData.searchData;

      this.searchOnFieldsCount = 0;
      this.searchOnFieldsButtons = new Array<Button | ButtonGroup>();
      if (this.searchData && !CollectionUtils.isEmpty(this.searchData)) {
        for (let cnt = 0; cnt < Object.keys(this.searchData).length; cnt++) {
          if (!StringUtils.isEmpty(this.searchData[Object.keys(this.searchData)[cnt]])) {
            this.searchOnFieldsCount++; 

            let filterButton: Button | ButtonGroup = this.setSelectedFilterButton(Object.keys(this.searchData)[cnt], this.searchData[Object.keys(this.searchData)[cnt]]);
            this.searchOnFieldsButtons.push(filterButton);
          }
        }
      }

      if (this._configData.configPerTabs) {
        let tabIndex = 0;
        this._configData.configPerTabs.forEach((tabConfigData: CrudListTabData, tabKey: string) => {
          if ((tabKey == tabIdentifier || (tabIndex == 0 && tabIdentifier == null)) && tabConfigData) {
            if (tabConfigData.badges) {
              this.badges = tabConfigData.badges;
            }
            if (tabConfigData.pageBackRoute) {
              this.pageBackRoute = tabConfigData.pageBackRoute;
            }
            if (tabConfigData.records && tabConfigData.records.length > 0) {
              this.records = this._configData.records;
            }
          }
          tabIndex++;
        })
      }
    }
  }

  getTabIdentifier(tabTitle: string): string {
    let tabIdentifier = '';
    if (this.listConfig && this.listConfig.displayType == CrudListDisplayType.TAB) {
      for (let index = 0; index < this.listConfig.lists.length; index++) {
        if (this.listHeaders[index].title == tabTitle) {
          tabIdentifier = this.listConfig.lists[index].identifier;
          break;
        }
      }
    }

    return tabIdentifier;
  }

  setListHeaders(): void {
    if (this.listConfig.lists) {
      for (let index = 0; index < this.listConfig.lists.length; index++) {
        this.listHeaders.push(this.listConfig.lists[index].header);
        // this.listConfig.lists[index].header = "";
      }
    }
  }

  setCrudHeader(): void {
    let title = "";

    if (this.header) {
      title = this.header.title;
    }

    if (this.listConfig.lists && this.listConfig.lists.length == 1 && this.listConfig.lists[0].header && this.listConfig.lists[0].header.title) {
      title = this.listConfig.lists[0].header.title;

      this.listConfig.lists[0].header.title = "";
    }
    this.title = title;
  }

  displaySearchForm(): void {
    if (this.listConfig && this.listConfig.lists && this.listConfig.lists.length > 0) {
      for (let lConfig of this.listConfig.lists) {
        if (lConfig.listType == ListType.DYNAMIC && this.searchConfig) {
          this.showSearchForm = true;
          break;
        }
      }
    }
  }

  setSearchButton(identifier: string, label: string, icon: string): Button {
    return {
      identifier: identifier,
      label: label,
      color: ButtonColor.PRIMARY,
      size: ButtonSize.SMALL,
      icon: icon,
      type: ButtonType.GHOST,
      onlyIcon: true,
    };
  }

  setSelectedFilterButton(identifier: string, label: string): Button | ButtonGroup {
    return {
      identifier: identifier,
      groupIdentifier: ReservedButton.CLEAR_FILTER_FIELD,
      label: label,
      color: ButtonColor.PRIMARY,
      size: ButtonSize.TINY,
      icon: "close",
      iconPosition: IconPosition.RIGHT,
      type: ButtonType.STROKED,
    };
  }

  @ViewChild('sidenav') sidenav: MatSidenav;
  close() {
    this.sidenav.close();
  }


  fieldChange(fieldChange: FieldChange) {
    console.log(fieldChange);
    this.onFieldChange.emit(fieldChange);
  }

  formChange(form: any) {
    this.onFormChange.emit(form);
  }

  buttonClick(event: any) {
    console.log(event);
    this.onButtonClick.emit(event);
  }

  buttonIconClick(event: any)  {
    this.onButtonIconClick.emit(event);
  }

  onSort(event: any) {
    console.log(event);
    this.onSortClick.emit(event);
  }

  onPage(event: any) {
    console.log(event);
    this.onPageClick.emit(event);
  }

  onAccordianChange(event) {
    console.log(event);
  }

  onTabChange(event: any) {
    console.log(event);

    this.setConfigAsPerTab(this.getTabIdentifier(event.tab.textLabel));
  }

  openBottomSheet(): void {
    let dialogRef = this._bottomSheet.open(BottomSearchSheet, {
      data: { searchConfig: this.searchConfig, searchData: this.searchData, reset: this.formReset },
    });

    this.buttonSubscriber = dialogRef.instance.onButtonClick.subscribe(event => this.buttonClick(event));
    this.fieldSubscriber = dialogRef.instance.onFieldChange.subscribe(event => this.fieldChange(event));
    this.formSubscriber = dialogRef.instance.onFormChange.subscribe(event => this.formChange(event));

    this.dialogRefSubscriber = dialogRef.afterDismissed().subscribe(() => {
      this.bottomUnsubscribe();
    });
  }

  ngOnDestroy() {
    this.bottomUnsubscribe();
    if (this.dialogRefSubscriber) {
      this.dialogRefSubscriber.unsubscribe();
    }
  }

  bottomUnsubscribe() {
    if (this.buttonSubscriber) {
      this.buttonSubscriber.unsubscribe();
    }
    if (this.fieldSubscriber) {
      this.fieldSubscriber.unsubscribe();
    }
    if (this.formSubscriber) {
      this.formSubscriber.unsubscribe();
    }
  }
}

@Component({
  selector: 'bottom-search-sheet',
  templateUrl: 'bottom-search-sheet.html',
  styleUrls: ['./bottom-search-sheet.scss']
})
export class BottomSearchSheet {
  onFormChange = new EventEmitter();
  @Output() onFieldChange = new EventEmitter();
  @Output() onButtonClick = new EventEmitter();

  constructor(private _bottomSheetRef: MatBottomSheetRef<BottomSearchSheet>, @Inject(MAT_BOTTOM_SHEET_DATA) public data: any) {
    console.log(this.data)
  }

  openLink(event: MouseEvent): void {
    this._bottomSheetRef.dismiss();
    event.preventDefault();
  }

  fieldChange(fieldChange: FieldChange) {
    this.onFieldChange.emit(fieldChange);
  }

  formChange(form: any) {
    this.onFormChange.emit(form);
  }

  buttonClick(event: any) {
    console.log(event);
    this.onButtonClick.emit(event);
  }

}