import { Button, ButtonGroup, ButtonType, HoverButton, Action, ObjectTree } from '../button/model';
import { FormDiaplyMode } from '../form/model';
import { CollectionUtils } from './collection.utility';

export class ButtonUtils {
    static instanceOfButton(object: any): object is Button {
        return 'type' in object &&
//            !('groupIdentifier' in object) &&
            (object['type'] != ButtonType.CHIP && object['type'] != ButtonType.GROUP && object['type'] != ButtonType.DROPDOWN) &&
            (object['type'] == ButtonType.FLAT || object['type'] == ButtonType.GHOST || object['type'] == ButtonType.RAISED || object['type'] == ButtonType.FAB || object['type'] == ButtonType.STROKED)
    }

    static instanceOfChipButton(object: any): object is ButtonGroup {
        return 'type' in object && 'groupIdentifier' in object && object['type'] == ButtonType.CHIP;
    }

    static instanceOfButtonGroup(object: any): object is ButtonGroup {
        return 'type' in object && 'groupIdentifier' in object && object['type'] == ButtonType.GROUP;
    }

    static instanceOfHoverButton(object: any): object is HoverButton {
        return 'type' in object && 'groupIdentifier' in object && object['type'] == ButtonType.DROPDOWN;
    }

    static instanceOfAnyButtonType(object: any): boolean {
        let isButton: boolean = false;
        if (this.instanceOfButton(object) || this.instanceOfChipButton(object) || this.instanceOfButtonGroup(object) || this.instanceOfHoverButton(object)) {
            isButton = true;
        }

        return isButton;
    }

    static hasWidth(buttons: Array<Button>): boolean {
        let hasButtonWidth: boolean = false;

        if (!CollectionUtils.isEmpty(buttons) && buttons.filter(button => button.width != null && button.width != "").length > 0) {
            hasButtonWidth = true;
        }

        return hasButtonWidth;
    }

    static setEqualWidth(buttons: Array<Button>): void {
        if (!CollectionUtils.isEmpty(buttons)) {
            let buttonCount = buttons.length;
            let buttonWidth = 100 / buttonCount;

            buttons.forEach(button => {
                button.width = "" + buttonWidth;
            });
        }
    }

    static getAction(sourceIdentifier: string, sourceIndex: number, widgetArrayIndex: number, identifier: string, parentHierarchy: ObjectTree, event: any, originalData: any, context: any, actionDialog: any): Action {
        let data = null;
        if (originalData) {
            data = { ...originalData, ...context };
        } else {
            data = context;
            originalData = context;
        }
        let action: Action = {
            sourceIdentifier: sourceIdentifier,
            sourceIndex: sourceIndex,
            widgetArrayIndex: widgetArrayIndex,
            action: identifier,
            actionData: actionDialog,
            data: data,
            originalData: originalData,
            parentHierarchy: parentHierarchy,
            event: event
        };

        return action;
    }
    
    static isDisable(displayMode: FormDiaplyMode): boolean {
        if (displayMode == FormDiaplyMode.BLOCKED || displayMode == FormDiaplyMode.VIEW) {
          return true;
        } else {
          return false;
        }
      }
    }