import { Form, FormDiaplyMode } from '../form/model';
import { CollectionUtils } from './collection.utility';
import { Crud, CrudWidgetType, CrudTab } from '../crud/model';
import { FormUtils } from './form.utility';
import { ListUtils } from './list.utility';
import { List } from '../list/model';
import { KeyMap } from '../field/model';

export class CrudUtils {
  static setOptionsUsingKey(crud: Crud, fieldKey: string, masterDataKey: string) {
    if (!CollectionUtils.isEmpty(crud) && !CollectionUtils.isEmpty(crud.form) && !CollectionUtils.isEmpty(crud.form.tabs)) {
      crud.form.tabs.forEach(tab => {
        if (!CollectionUtils.isEmpty(tab) && !CollectionUtils.isEmpty(tab.widgets)) {
          tab.widgets.forEach(widget => {
            if (!CollectionUtils.isEmpty(widget) && widget.widgetType == CrudWidgetType.FORM) {
              FormUtils.setOptionsUsingKey(<Form>widget.widget, fieldKey, masterDataKey);
            }
            if (!CollectionUtils.isEmpty(widget) && widget.widgetType == CrudWidgetType.LIST) {
              ListUtils.setOptionsUsingKey(<List>widget.widget, fieldKey, masterDataKey);
            }
          })
        }
      })
    }

    if (!CollectionUtils.isEmpty(crud) && !CollectionUtils.isEmpty(crud.list) && !CollectionUtils.isEmpty(crud.list.lists)) {
      crud.list.lists.forEach(list => {
        if (!CollectionUtils.isEmpty(list)) {
          ListUtils.setOptionsUsingKey(list, fieldKey, masterDataKey);
        }
      })
    }

    if (!CollectionUtils.isEmpty(crud) && !CollectionUtils.isEmpty(crud.search) && !CollectionUtils.isEmpty(crud.search.form))  {
      FormUtils.setOptionsUsingKey(crud.search.form, fieldKey, masterDataKey);
    }
  }

  static setOptionsUsingValues(crud: Crud, fieldKey: string, keyMap: KeyMap, relaodAll: boolean, record: any) {
    if (!CollectionUtils.isEmpty(crud) && !CollectionUtils.isEmpty(crud.form) && !CollectionUtils.isEmpty(crud.form.tabs)) {
      crud.form.tabs.forEach(tab => {
        if (!CollectionUtils.isEmpty(tab) && !CollectionUtils.isEmpty(tab.widgets)) {
          tab.widgets.forEach(widget => {
            if (!CollectionUtils.isEmpty(widget) && widget.widgetType == CrudWidgetType.FORM) {
              FormUtils.setOptionsUsingValues(<Form>widget.widget, fieldKey, keyMap, relaodAll, record);
            }
            if (!CollectionUtils.isEmpty(widget) && widget.widgetType == CrudWidgetType.LIST) {
              ListUtils.setOptionsUsingValues(<List>widget.widget, fieldKey, keyMap, relaodAll, record);
            }
          })
        }
      })
    }

    if (!CollectionUtils.isEmpty(crud) && !CollectionUtils.isEmpty(crud.list) && !CollectionUtils.isEmpty(crud.list.lists)) {
      crud.list.lists.forEach(list => {
        if (!CollectionUtils.isEmpty(list)) {
          ListUtils.setOptionsUsingValues(list, fieldKey, keyMap, relaodAll, record);
        }
      })
    }

    if (!CollectionUtils.isEmpty(crud) && !CollectionUtils.isEmpty(crud.search) && !CollectionUtils.isEmpty(crud.search.form))  {
      FormUtils.setOptionsUsingValues(crud.search.form, fieldKey, keyMap, relaodAll, record);
    }
  }

  static setDisplayType(crudTabs: Array<CrudTab>, formDisplayMode: FormDiaplyMode) {
    if (!CollectionUtils.isEmpty(crudTabs)) {
      crudTabs.forEach(tab => {
        if (!CollectionUtils.isEmpty(tab) && !CollectionUtils.isEmpty(tab.widgets)) {
          tab.widgets.forEach(widget => {
            if (!CollectionUtils.isEmpty(widget) && widget.widgetType == CrudWidgetType.FORM) {
              (<Form>widget.widget).displayMode = formDisplayMode;
            }
          })
        }
      })
    }
  }

  static changeButtonLabelIcon(crudTabs: Array<CrudTab>, buttonIdentifier: string, label?: string, icon?: string)  {
    if (!CollectionUtils.isEmpty(crudTabs)) {
      crudTabs.forEach(tab => {
        if (!CollectionUtils.isEmpty(tab) && !CollectionUtils.isEmpty(tab.widgets)) {
          tab.widgets.forEach(widget => {
            if (!CollectionUtils.isEmpty(widget) && widget.widgetType == CrudWidgetType.FORM) {
              FormUtils.changeButtonLabelIcon((<Form>widget.widget), buttonIdentifier, label, icon);
            }
          })
        }
      })
    }
  }
}