import { Component, OnInit, OnDestroy, Input, EventEmitter, Output, ViewEncapsulation } from '@angular/core';

import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';

import { Form, FormDiaplyMode, FormField, FormLayout, FormLayoutRow, FormSectionExpand, FormButton, FormButtonLayout } from '../../model';
import { Field, FieldChange, KeyMap, KeyMapOptionType } from '../../../field/model';
import { FormGroup } from '@angular/forms';
import { Action } from '../../../button/model';
import { FormUtils, ObjectUtils, KeyMapUtils, ButtonUtils, CollectionUtils, FormCustomUtils, DependentUtils } from '../../../utility';
import { AbilityUtils } from '../../../utility/ability.utility';
import { Ability } from '@casl/ability';
import { Subscription } from 'rxjs';

@Component({
  selector: 'mx-form', 
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FormComponent implements OnInit, OnDestroy {
  @Input() _formConfig: Form;
  get formConfig(): any {
    return this._formConfig;
  }
  @Input()
  set formConfig(_formConfig: any) {
    this._formConfig = _formConfig;
    // this.headerReload = false;

    while(1)  {
      if (this.formInitialized) {
        // setTimeout(() => {this.headerReload = true;}, 20);
        console.log("1");
        this.init();
        break;
      }
    }
  }

  @Input() sourceIdentifier: string;
  @Input() sourceIndex: number;
  @Input() widgetArrayIndex: number;
  @Input() originalData: any;
  @Input() keyMap: Array<KeyMap>; 
  @Output() onFormChange = new EventEmitter();
  @Output() onFieldChange = new EventEmitter();
  @Output() onButtonClick = new EventEmitter();

  formFields: Array<FormField> = new Array<FormField>();
  dependencies: any;

  cellCount: number = 12;
  isMobile: boolean;
  isTablet: boolean;
  isDesktop: boolean;
  showButtons: boolean = true;

  private _supportingRecord: any;
  get supportingRecord(): any {
    return this._supportingRecord;
  }
  @Input()
  set supportingRecord(_supportingRecord: any) {
    this._supportingRecord = _supportingRecord;

    this.initFormGroup();
  }

  private _record: any;
  get record(): any {
    return this._record;
  }
  @Input()
  set record(_record: any) {
    this._record = _record;

    if (this.originalData) { } else  {
      this.originalData = this._record;
    }
    this.initFormGroup();
  }

  private _reset: boolean;
  get reset(): boolean {
    return this._reset;
  }
  @Input()
  set reset(_reset: boolean) {
    this._reset = _reset;

    if (this._reset == true) {
      FormUtils.reset(this.form, this.formConfig);
    }
  }

  @Input()
  form: FormGroup;
  formLayouts: Array<FormLayout> = new Array<FormLayout>();

  formButton: FormButton;
  formButtonLayout: FormButtonLayout;
  breakpointSubscription: Subscription;

  formInitialized: boolean = true;
  // headerReload: boolean = false;

  //  unflatten = require('flat').unflatten;

  constructor(private ability: Ability, private breakpointObserver: BreakpointObserver) {
    AbilityUtils.setAbility(this.ability);
  }

  ngOnInit() {
    this.formInitialized = true;
  }

  init()  {
    this.getLayout();
    this.getActions();
    //    this.fieldDefaultSetting();
    this.dependencies = { ...DependentUtils.getDependencyTree(this.formConfig.formFields), ...DependentUtils.getDependencyTreeForButton(this.formConfig.action) };

    this.getCustomLayout();
  }

  getCustomLayout(): void {
    if (this.formConfig.showCustomLayout) {
      FormCustomUtils.setCustomLayout(this.formConfig, this.formConfig.layout);  
    } else  {
      this.formConfig.showCustomLayout = false;
    }
  }

  getActions(): void {
    if (this.formConfig && this.formConfig.action && (<FormButton>this.formConfig.action).align && (<FormButton>this.formConfig.action).buttons && (<FormButton>this.formConfig.action).buttons.length > 0) {
      this.formButton = (<FormButton>this.formConfig.action);
    }

    if (this.formConfig && this.formConfig.action && (<FormButtonLayout>this.formConfig.action).cells && (<FormButtonLayout>this.formConfig.action).cells.length > 0) {
      this.formButtonLayout = (<FormButtonLayout>this.formConfig.action);
    }
  }

  dependentFieldChange(action: Action) {
    if (this.formConfig.identifier == action.sourceIdentifier) {
      let newRecord = ObjectUtils.unflatten(this.form.getRawValue());
      this.record = { ...this.record, ...newRecord };
      console.log(this.record);
      this.initFormGroup();

      this.showButtons = false;
      setTimeout(() => {
        this.showButtons = true;
      }, 50);
    }
  }

  fieldChange(fieldChange: FieldChange) {
    this.onFieldChange.emit(fieldChange);
    this.onFormChange.emit(this.form);
  }

  buttonClick(event: any) {
    this.onButtonClick.emit(event);
  }

  fieldDefaultSetting(): void {
    if (this.formConfig) {
      for (let formField of this.formConfig.formFields) {
        if (!(<Field>formField.field).fieldDisplayType) {
          (<Field>formField.field).fieldDisplayType = this.formConfig.displayType;
        }
      }

      if (!this.formConfig.displayInColumns) {
        this.formConfig.displayInColumns = 1;
      }
    }

    this.setTitle();
  }

  setTitle() {
    if (this.formConfig.header) {
      let title = this.formConfig.header.title;
      switch (this.formConfig.displayMode) {
        case FormDiaplyMode.ADD:
          if (this.formConfig.header.addModeTitle) {
            title = this.formConfig.header.addModeTitle;
          }
          break;
        case FormDiaplyMode.EDIT:
          if (this.formConfig.header.editModeTitle) {
            title = this.formConfig.header.editModeTitle;
          }
          break;
        case FormDiaplyMode.VIEW:
          if (this.formConfig.header.viewModeTitle) {
            title = this.formConfig.header.viewModeTitle;
          }
          break;
        case FormDiaplyMode.BLOCKED:
          if (this.formConfig.header.viewModeTitle) {
            title = this.formConfig.header.viewModeTitle;
          }
          break;
      }
      this.formConfig.header.title = title;
    }
  }

  initFormGroup(): void {
    KeyMapUtils.setOptionssUsingValues(this.keyMap, false, KeyMapOptionType.FORM, this.formConfig, this._record);

    this.form = FormUtils.initFormGroup(this.formConfig.formFields, this._supportingRecord, this._record, this.formConfig.displayMode);

    this.onFormChange.emit(this.form);

    if (this.isMobile || this.isTablet || this.isDesktop) {
      this.resetDisplayInColumn();
    }
  }

  // isValidButton(button: Button): boolean {
  //   let isValid: boolean = false;

  //   if (button.displayInFormModes.indexOf(this.formConfig.displayMode) > -1) {
  //     isValid = true;
  //   }
  //   return isValid;
  // }

  // isNewRow(index: number): string {
  //   let displayRow: string = "";
  //   if (index % this.formConfig.displayInColumns == 0) {
  //     displayRow = "row";
  //   }

  //   return displayRow;
  // }

  getFormValue(): any {
    return FormUtils.getRawValue(this.form);
  }

  transformRows(): void {
    let sectionLabel: string = "";
    let formLayouts: Array<FormLayout> = new Array<FormLayout>();
    let index: number = -1;

    let displayInColumns = this.formConfig.displayInColumns;

    if (this.formConfig.formFields && this.form) {
      for (let formField of this.formConfig.formFields) { 
        if (this.form.controls[formField.field.key] && DependentUtils.displayDependencyField(formField.field.dependentOnFields, this._supportingRecord, this._record)) {
          if (formField.separator || sectionLabel == "") {
            formLayouts.push({
              sectionLabel: formField && formField.separator && formField.separator.label ? formField.separator.label : "",
              sectionIcon: formField && formField.separator && formField.separator.icon ? formField.separator.icon : "",
              sectionExpandable: formField && formField.separator && formField.separator.expandable ? formField.separator.expandable : null,
              rows: new Array<FormLayoutRow>()
            });
            sectionLabel = "-";
            index++;
          }

          if (formLayouts[index]) {
            let rLength = formLayouts[index].rows.length;

            if (formLayouts[index].rows.length == 0) {
              formLayouts[index].rows.push({ columns: new Array<FormField>() });
              formLayouts[index].rows[0].columns = new Array<FormField>();
            }

            let cLength = this.formFieldRowColumnCount(formLayouts, index);

            if (cLength >= displayInColumns) {
              formLayouts[index].rows[rLength] = { columns: new Array<FormField>() };
            }

            formLayouts[index].rows[formLayouts[index].rows.length - 1].columns.push(formField);
          }
        }
      }
    }

    this.formLayouts = formLayouts;
  }

  private formFieldRowColumnCount(formLayouts: Array<FormLayout>, index: number): number {
    let colCount = 0;

    for (let cCnt = 0; cCnt < formLayouts[index].rows[formLayouts[index].rows.length - 1].columns.length; cCnt++) {
      if (formLayouts[index].rows[formLayouts[index].rows.length - 1].columns[cCnt].displayInColumns) {
        colCount += formLayouts[index].rows[formLayouts[index].rows.length - 1].columns[cCnt].displayInColumns;
      } else {
        colCount += 1;
        formLayouts[index].rows[formLayouts[index].rows.length - 1].columns[cCnt].displayInColumns = 1;
      }
    }

    return colCount;
  }

  toggleList(formLayout: FormLayout): void {
    if (formLayout.sectionExpandable && formLayout.sectionExpandable.allowed) {
      if (formLayout.sectionExpandable.default == FormSectionExpand.EXPAND) {
        formLayout.sectionExpandable.default = FormSectionExpand.COLLAPSE;
      } else {
        formLayout.sectionExpandable.default = FormSectionExpand.EXPAND;
      }
    }
  }

  getLayout(): void {
    this.breakpointSubscription = this.breakpointObserver.observe([
      Breakpoints.XSmall,
      Breakpoints.Small,
      Breakpoints.Medium,
      Breakpoints.Large,
      Breakpoints.XLarge
    ]).subscribe((state: BreakpointState) => {
      if (state.breakpoints[Breakpoints.XSmall]) {
        this.isMobile = true;
        console.log('Matches XSmall viewport');
      }
      if (state.breakpoints[Breakpoints.Small]) {
        this.isTablet = true;
        console.log('Matches Small viewport');
      }
      if (state.breakpoints[Breakpoints.Medium]) {
        this.isDesktop = true;
        console.log('Matches Medium  viewport');
      }
      if (state.breakpoints[Breakpoints.Large]) {
        this.isDesktop = true;
        console.log('Matches Large viewport');
      }
      if (state.breakpoints[Breakpoints.XLarge]) {
        this.isDesktop = true;
        console.log('Matches XLarge viewport');
      }

      this.resetButtonDisplay();
      this.resetDisplayInColumn();
    });
  }

  resetButtonDisplay(): void {
    if (this.formConfig && this.formConfig.action && !CollectionUtils.isEmpty((<FormButton>this.formConfig.action).buttons)) {
      if ((this.isMobile || this.isTablet) && !ButtonUtils.hasWidth((<FormButton>this.formConfig.action).buttons)) {
        ButtonUtils.setEqualWidth((<FormButton>this.formConfig.action).buttons);
      }
    } 
  }

  resetDisplayInColumn(): void {
    if (this.formConfig && this.formConfig.displayInColumns) {
      if (this.isMobile) {
        this.cellCount = 4;
        this.formConfig.displayInColumns = this.getNewDisplayInColumn("mobile", this.formConfig.displayInColumns);
      }
      if (this.isTablet) {
        this.cellCount = 8;
        this.formConfig.displayInColumns = this.getNewDisplayInColumn("tablet", this.formConfig.displayInColumns);
      }
    }

    if (this.formConfig && !CollectionUtils.isEmpty(this.formConfig.formFields)) {
      this.formConfig.formFields.forEach(formField => {
        if (formField.displayInColumns) {
          if (this.isMobile) {
            this.cellCount = 4;
            formField.displayInColumns = this.getNewDisplayInColumn("mobile", formField.displayInColumns);
          }
          if (this.isTablet) {
            this.cellCount = 8;
            formField.displayInColumns = this.getNewDisplayInColumn("tablet", formField.displayInColumns);
          }
        } else  { 
          formField.displayInColumns = 1;
        }
      })
    }

    this.fieldDefaultSetting();
    this.transformRows();
  }

  getNewDisplayInColumn(device: string, displayInColumn: number): number {
    let displayInColumnMap: Map<string, Map<number, number>> = new Map<string, Map<number, number>>();

    let mobileMap: Map<number, number> = new Map<number, number>();
    mobileMap.set(12, 1);
    mobileMap.set(11, 1);
    mobileMap.set(10, 1);
    mobileMap.set(9, 1);
    mobileMap.set(8, 1);
    mobileMap.set(7, 1);
    mobileMap.set(6, 1);
    mobileMap.set(5, 1);
    mobileMap.set(4, 1);
    mobileMap.set(3, 1);
    mobileMap.set(2, 1);
    mobileMap.set(1, 1);
    displayInColumnMap.set("mobile", mobileMap);

    let tabletMap: Map<number, number> = new Map<number, number>();
    tabletMap.set(12, 2);
    tabletMap.set(11, 2);
    tabletMap.set(10, 2);
    tabletMap.set(9, 2);
    tabletMap.set(8, 2);
    tabletMap.set(7, 2);
    tabletMap.set(6, 2);
    tabletMap.set(5, 2);
    tabletMap.set(4, 2);
    tabletMap.set(3, 2);
    tabletMap.set(2, 2);
    tabletMap.set(1, 1);
    displayInColumnMap.set("tablet", tabletMap);

    return displayInColumnMap.get(device).get(displayInColumn);
  }

  ngOnDestroy(): void {
    if (this.breakpointSubscription) {
      this.breakpointSubscription.unsubscribe();
    }
  }
}
